  width: 260px;
  border-right: 1px solid var(--el-border-color-lighter);
  display: flex;
  flex-direction: column;
  background-color: var(--el-bg-color-page);
}

.new-chat-btn {
  margin: 16px;
}

.model-selector {
  padding: 0 16px 16px;
}

.model-select {
  width: 100%;
}

.model-option {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px 0;
}

.model-info {
  flex: 1;
}

.model-name {
  font-weight: 500;
  margin-bottom: 4px;
}

.model-desc {
  font-size: 12px;
  color: var(--el-text-color-secondary);
}

.chat-history {
  flex: 1;
  overflow-y: auto; /*  */
  padding: 0 8px;
}

.history-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 8px 12px;
  margin: 4px 0;
  border-radius: 6px;
  cursor: pointer;
  transition: all 0.3s;
}

.history-item:hover {
  background-color: var(--el-fill-color-light);
}

.history-item.active {
