<template>
  <div class="web-view">
    <div class="web-header">
      <div class="url-bar">
        <el-input
          v-model="currentUrl"
          placeholder=""
          clearable
          @keyup.enter="loadUrl"
        >
          <template #prefix>
            <el-icon><Link /></el-icon>
          </template>
        </el-input>
      </div>
      <div class="controls">
        <el-button-group>
          <el-button @click="refresh">
            <el-icon><Refresh /></el-icon>
          </el-button>
          <el-button @click="openInNewTab">
            <el-icon><Position /></el-icon>
          </el-button>
        </el-button-group>
      </div>
    </div>

    <div class="web-content">
      <el-alert
        v-if="showError"
        title="iframe"
        type="warning"
        description="''"
        show-icon
        :closable="false"
        style="margin-bottom: 16px"
      />
      
      <iframe
        ref="iframeRef"
        :src="currentUrl"
        class="iframe-content"
        @load="handleLoad"
        @error="handleError"
        sandbox="allow-same-origin allow-scripts allow-popups allow-forms"
      ></iframe>
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted } from 'vue'
import { useRoute } from 'vue-router'
import { Link, Refresh, Position } from '@element-plus/icons-vue'

const route = useRoute()
const iframeRef = ref(null)
const currentUrl = ref('')
const showError = ref(false)

onMounted(() => {
  // URL
  currentUrl.value = route.query.url || ''
  if (currentUrl.value) {
    loadUrl()
  }
})

const loadUrl = () => {
  currentUrl.value = route.query.url || ''
  if (!currentUrl.value) return
  showError.value = false
  // URLhttphttpshttps://
  if (!/^https?:\/\//i.test(currentUrl.value)) {
    currentUrl.value = 'https://' + currentUrl.value
  }
}

const refresh = () => {
  const iframe = iframeRef.value
  if (iframe) {
    // URL
    const currentSrc = iframe.src
    // src
    iframe.src = ''
    // src
    setTimeout(() => {
      iframe.src = currentSrc
    }, 100)
  }
}

const openInNewTab = () => {
  if (currentUrl.value) {
    window.open(currentUrl.value, '_blank')
  }
}

const handleLoad = () => {
  showError.value = false
}

const handleError = () => {
  showError.value = true
}
</script>

<style scoped>
.web-view {
  display: flex;
  flex-direction: column;
  height: calc(100vh - 84px); /* padding */
  padding: 16px;
}

.web-header {
  display: flex;
  gap: 16px;
  margin-bottom: 16px;
}

.url-bar {
  flex: 1;
}

.web-content {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.iframe-content {
  flex: 1;
  width: 100%;
  border: 1px solid var(--el-border-color-light);
  border-radius: 4px;
  background: white;
}

.controls {
  display: flex;
  align-items: center;
}
</style> 