<template>
  <el-menu
    :default-active="activeMenu"
    class="app-menu"
    :collapse="isCollapse"
  >
    <div class="collapse-btn" @click="toggleCollapse">
      <el-icon>
        <component :is="isCollapse ? 'Expand' : 'Fold'" />
      </el-icon>
    </div>
    
    <RenderMenuNodes :menu-nodes="menuNodes" />
  </el-menu>
</template>

<script setup>
import { ref, computed, watch } from 'vue'
import { useRouter, useRoute } from 'vue-router'
import RenderMenuNodes from './RenderMenuNodes.vue'
import { Expand, Fold } from '@element-plus/icons-vue'

const router = useRouter()
const route = useRoute()

// 
const activeMenu = computed(() => route.path)

// 
const isCollapse = ref(false)

// 
const menuNodes = computed(() => {
  const menuConfig = localStorage.getItem('menu-config')
  if (menuConfig) {
    return JSON.parse(menuConfig)
  }
  return []
})

// 
const toggleCollapse = () => {
  isCollapse.value = !isCollapse.value
}

// 
const emit = defineEmits(['collapse-change'])

// 
watch(isCollapse, (newValue) => {
  emit('collapse-change', newValue)
})
</script>

<style scoped>
.app-menu {
  height: 100%;
  border-right: none;
  transition: width 0.3s;
  width: 200px;
}

.app-menu.el-menu--collapse {
  width: 64px;
}

.collapse-btn {
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  border-bottom: 1px solid var(--el-border-color-light);
}

.collapse-btn:hover {
  background-color: var(--el-menu-hover-bg-color);
}
</style> 