<!-- eslint-disable vue/multi-word-component-names -->
<template>
  <div class="dashboard-container">
    <!--  -->
    <div class="stats-container">
      <el-row :gutter="20">
        <el-col :span="6" v-for="stat in stats" :key="stat.title">
          <el-card 
            class="stat-card" 
            :class="[stat.class, 'clickable']" 
            shadow="hover"
            @click="handleStatClick(stat.type)"
          >
            <div class="stat-content">
              <div class="stat-icon">
                <el-icon><component :is="stat.icon" /></el-icon>
              </div>
              <div class="stat-info">
                <div class="stat-title">{{ stat.title }}</div>
                <div class="stat-number">{{ stat.value }}</div>
                <div class="stat-desc">{{ stat.description }}</div>
              </div>
            </div>
          </el-card>
        </el-col>
      </el-row>
    </div>

    <!--  -->
    <el-row :gutter="20" class="charts-container">
      <!--  -->
      <el-col :span="16">
        <el-card class="chart-card">
          <template #header>
            <div class="chart-header">
              <span></span>
              <el-radio-group v-model="trendTimeRange" size="small">
                <el-radio-button label="week"></el-radio-button>
                <el-radio-button label="month"></el-radio-button>
                <el-radio-button label="year"></el-radio-button>
              </el-radio-group>
            </div>
          </template>
          <v-chart class="chart" :option="taskTrendOption" autoresize />
        </el-card>
      </el-col>

      <!--  -->
      <el-col :span="8">
        <el-card class="chart-card">
          <template #header>
            <div class="chart-header">
              <span></span>
            </div>
          </template>
          <v-chart class="chart" :option="goalProgressOption" autoresize />
        </el-card>
      </el-col>
    </el-row>

    <!--  bottom-container  -->
    <el-row :gutter="20" class="review-container">
      <el-col :span="24">
        <el-card class="review-card">
          <template #header>
            <div class="card-header">
              <span></span>
              <div class="review-actions">
                <el-button-group>
                  <el-button 
                    type="primary" 
                    :plain="reviewType !== 'daily'"
                    @click="reviewType = 'daily'"
                  >
                    
                  </el-button>
                  <el-button 
                    type="primary" 
                    :plain="reviewType !== 'weekly'"
                    @click="reviewType = 'weekly'"
                  >
                    
                  </el-button>
                  <el-button 
                    type="primary" 
                    :plain="reviewType !== 'monthly'"
                    @click="reviewType = 'monthly'"
                  >
                    
                  </el-button>
                </el-button-group>
                <el-button type="primary" @click="openReviewDialog">
                  <el-icon><plus /></el-icon>
                </el-button>
              </div>
            </div>
          </template>
          
          <!--  -->
          <el-timeline>
            <el-timeline-item
              v-for="review in filteredReviews"
              :key="review.id"
              :timestamp="review.date"
              :type="getReviewType(review.type)"
            >
              <el-card class="review-item">
                <template #header>
                  <div class="review-item-header">
                    <el-tag :type="getReviewTagType(review.type)">
                      {{ getReviewTypeText(review.type) }}
                    </el-tag>
                    <span class="review-title">{{ review.title }}</span>
                  </div>
                </template>
                <div class="review-content">
                  <div v-if="review.achievements" class="review-section">
                    <h4> </h4>
                    <p>{{ review.achievements }}</p>
                  </div>
                  <div v-if="review.lessons" class="review-section">
                    <h4> </h4>
                    <p>{{ review.lessons }}</p>
                  </div>
                  <div v-if="review.plans" class="review-section">
                    <h4> </h4>
                    <p>{{ review.plans }}</p>
                  </div>
                </div>
              </el-card>
            </el-timeline-item>
          </el-timeline>
        </el-card>
      </el-col>
    </el-row>

    <!--  -->
    <el-row :gutter="20" class="bottom-container">
      <!--  -->
      <el-col :span="12">
        <el-card class="timeline-card">
          <template #header>
            <div class="card-header">
              <span></span>
            </div>
          </template>
          <el-timeline>
            <el-timeline-item
              v-for="activity in recentActivities"
              :key="activity.id"
              :timestamp="activity.time"
              :type="activity.type"
            >
              {{ activity.content }}
            </el-timeline-item>
          </el-timeline>
        </el-card>
      </el-col>

      <!--  -->
      <el-col :span="12">
        <el-card class="heatmap-card">
          <template #header>
            <div class="card-header">
              <span></span>
              <el-tooltip content="">
                <el-icon><info-filled /></el-icon>
              </el-tooltip>
            </div>
          </template>
          <v-chart class="heatmap-chart" :option="heatmapOption" autoresize />
        </el-card>
      </el-col>
    </el-row>

    <!--  -->
    <el-card class="recent-todos">
      <template #header>
        <div class="card-header">
          <span></span>
          <el-button type="primary" link @click="goToTodo">
            
            <el-icon class="el-icon--right"><arrow-right /></el-icon>
          </el-button>
        </div>
      </template>
      <el-table :data="recentTodos" style="width: 100%">
        <el-table-column prop="text" label=""></el-table-column>
        <el-table-column prop="priority" label="" width="100">
          <template #default="{ row }">
            <el-tag :type="getPriorityType(row.priority)">{{ row.priority }}</el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="dueDate" label="" width="180">
          <template #default="{ row }">
            <span :class="{ 'text-danger': isOverdue(row.dueDate) }">
              {{ formatDate(row.dueDate) }}
            </span>
          </template>
        </el-table-column>
      </el-table>
    </el-card>

    <!--  -->
    <el-dialog
      v-model="reviewDialogVisible"
      :title="getReviewDialogTitle"
      width="60%"
    >
      <el-form :model="reviewForm" ref="reviewFormRef" :rules="reviewRules">
        <el-form-item label="" prop="type">
          <el-select v-model="reviewForm.type">
            <el-option label="" value="daily" />
            <el-option label="" value="weekly" />
            <el-option label="" value="monthly" />
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="title">
          <el-input v-model="reviewForm.title" placeholder="" />
        </el-form-item>
        <el-form-item label="" prop="achievements">
          <el-input
            v-model="reviewForm.achievements"
            type="textarea"
            :rows="3"
            placeholder=""
          />
        </el-form-item>
        <el-form-item label="" prop="lessons">
          <el-input
            v-model="reviewForm.lessons"
            type="textarea"
            :rows="3"
            placeholder=""
          />
        </el-form-item>
        <el-form-item label="" prop="plans">
          <el-input
            v-model="reviewForm.plans"
            type="textarea"
            :rows="3"
            placeholder=""
          />
        </el-form-item>
      </el-form>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="reviewDialogVisible = false"></el-button>
          <el-button type="primary" @click="submitReview">
            
          </el-button>
        </span>
      </template>
    </el-dialog>
  </div>
</template>

<script setup>
import { ref, computed, onMounted, watch } from 'vue'
import { useRouter } from 'vue-router'
import { use } from 'echarts/core'
import { CanvasRenderer } from 'echarts/renderers'
import {
  LineChart,
  PieChart,
  HeatmapChart,
} from 'echarts/charts'
import {
  GridComponent,
  TooltipComponent,
  LegendComponent,
  ToolboxComponent,
  CalendarComponent,
  VisualMapComponent,
} from 'echarts/components'
import VChart from 'vue-echarts'
import { ElMessage } from 'element-plus'
import {
  Plus,
  Timer,
  List,
  Check,
  Warning,
  InfoFilled,
  ArrowRight,
} from '@element-plus/icons-vue'
import { useReviewStore } from '@/stores/review'

//  ECharts 
use([
  CanvasRenderer,
  LineChart,
  PieChart,
  HeatmapChart,
  GridComponent,
  TooltipComponent,
  LegendComponent,
  ToolboxComponent,
  CalendarComponent,
  VisualMapComponent,
])

const router = useRouter()
const todos = ref([])
const trendTimeRange = ref('week')

const reviewStore = useReviewStore()

//  localStorage 
const loadTodos = () => {
  const stored = localStorage.getItem('todos')
  if (stored) {
    todos.value = JSON.parse(stored)
  }
}

//  localStorage 
const saveTodosToStorage = () => {
  localStorage.setItem('todos', JSON.stringify(todos.value))
}

//  todos  watch
watch(() => todos.value, () => {
  saveTodosToStorage()
}, { deep: true })

// 
const stats = computed(() => [
  {
    title: '',
    value: todos.value.filter(t => !t.completed).length,
    description: '',
    class: 'todo-card',
    icon: 'List',
    type: 'todo'
  },
  {
    title: '',
    value: getTotalPomodoros(),
    description: '',
    class: 'focus-card',
    icon: 'Timer',
    type: 'pomodoro'
  },
  {
    title: '',
    value: getCompletionRate(),
    description: getCompletionTrend(),
    class: 'completed-card',
    icon: 'Check',
    type: 'completed'
  },
  {
    title: '',
    value: getOverdueTasks().length,
    description: '',
    class: 'overdue-card',
    icon: 'Warning',
    type: 'overdue'
  }
])

// 
const getTotalPomodoros = () => {
  return todos.value.reduce((sum, task) => sum + (task.pomodoros || 0), 0)
}

// 
const getCompletionRate = () => {
  const total = todos.value.length
  if (total === 0) return '0%'
  const completed = todos.value.filter(t => t.completed).length
  return Math.round((completed / total) * 100) + '%'
}

// 
const getCompletionTrend = () => {
  // 
  const lastWeek = new Date()
  lastWeek.setDate(lastWeek.getDate() - 7)
  
  const oldTasks = todos.value.filter(t => t.createdAt < lastWeek.getTime())
  const oldCompleted = oldTasks.filter(t => t.completed).length
  const oldRate = oldTasks.length ? oldCompleted / oldTasks.length : 0
  
  const currentRate = parseFloat(getCompletionRate()) / 100
  const diff = currentRate - oldRate
  
  if (Math.abs(diff) < 0.01) return ''
  return diff > 0 ? ` +${Math.round(diff * 100)}%` : ` ${Math.round(diff * 100)}%`
}

// 
const getOverdueTasks = () => {
  return todos.value.filter(t => !t.completed && isOverdue(t.dueDate))
}

// 
const getCompletedTasksForDate = (date) => {
  if (trendTimeRange.value === 'year') {
    // 
    const year = date.getFullYear()
    const month = date.getMonth()
    const startDate = new Date(year, month, 1)
    const endDate = new Date(year, month + 1, 0)
    return todos.value.filter(task => 
      task.completed && 
      task.completedAt >= startDate.getTime() && 
      task.completedAt <= endDate.getTime()
    ).length
  } else {
    // 
    const nextDay = new Date(date)
    nextDay.setDate(date.getDate() + 1)
    return todos.value.filter(task => 
      task.completed && 
      task.completedAt >= date.getTime() && 
      task.completedAt < nextDay.getTime()
    ).length
  }
}

// 
const getNewTasksForDate = (date) => {
  if (trendTimeRange.value === 'year') {
    // 
    const year = date.getFullYear()
    const month = date.getMonth()
    const startDate = new Date(year, month, 1)
    const endDate = new Date(year, month + 1, 0)
    return todos.value.filter(task => 
      task.createdAt >= startDate.getTime() && 
      task.createdAt <= endDate.getTime()
    ).length
  } else {
    // 
    const nextDay = new Date(date)
    nextDay.setDate(date.getDate() + 1)
    return todos.value.filter(task => 
      task.createdAt >= date.getTime() && 
      task.createdAt < nextDay.getTime()
    ).length
  }
}

// 
const getDateRange = computed(() => {
  const dates = []
  const today = new Date()
  let year, month, daysInMonth, currentYear
  
  switch (trendTimeRange.value) {
    case 'week': {
      // 7
      for (let i = 6; i >= 0; i--) {
        const date = new Date()
        date.setDate(date.getDate() - i)
        date.setHours(0, 0, 0, 0)
        dates.push(date)
      }
      break
    }
      
    case 'month': {
      // 
      year = today.getFullYear()
      month = today.getMonth()
      daysInMonth = new Date(year, month + 1, 0).getDate()
      
      for (let i = 0; i < daysInMonth; i++) {
        const date = new Date(year, month, i + 1)
        date.setHours(0, 0, 0, 0)
        dates.push(date)
      }
      break
    }
      
    case 'year': {
      // 
      currentYear = today.getFullYear()
      for (let month = 0; month < 12; month++) {
        const date = new Date(currentYear, month, 1)
        dates.push(date)
      }
      break
    }
  }
  
  return dates
})

// 
const taskTrendOption = computed(() => {
  const dates = getDateRange.value
  const completedData = dates.map(date => getCompletedTasksForDate(date))
  const newData = dates.map(date => getNewTasksForDate(date))
  
  return {
    tooltip: {
      trigger: 'axis',
      formatter: function(params) {
        const date = dates[params[0].dataIndex]
        return formatDate(date) + '<br/>' +
          params.map(param => 
            param.seriesName + ': ' + param.value
          ).join('<br/>')
      }
    },
    legend: {
      data: ['', '']
    },
    grid: {
      left: '3%',
      right: '4%',
      bottom: '3%',
      containLabel: true
    },
    xAxis: {
      type: 'category',
      boundaryGap: false,
      data: dates.map(date => formatDateShort(date)),
      axisLabel: {
        interval: trendTimeRange.value === 'week' ? 0 : 'auto',
        rotate: trendTimeRange.value === 'month' ? 30 : 0
      }
    },
    yAxis: {
      type: 'value',
      minInterval: 1
    },
    series: [
      {
        name: '',
        type: 'line',
        data: completedData,
        smooth: true,
        areaStyle: {
          opacity: 0.1
        },
        itemStyle: {
          color: '#67C23A'
        },
        lineStyle: {
          width: 2
        }
      },
      {
        name: '',
        type: 'line',
        data: newData,
        smooth: true,
        areaStyle: {
          opacity: 0.1
        },
        itemStyle: {
          color: '#409EFF'
        },
        lineStyle: {
          width: 2
        }
      }
    ]
  }
})

// 
const formatDateShort = (date) => {
  switch (trendTimeRange.value) {
    case 'week':
      return `${date.getMonth() + 1}/${date.getDate()}`
    case 'month':
      return `${date.getDate()}`
    case 'year':
      return `${date.getMonth() + 1}`
    default:
      return `${date.getMonth() + 1}/${date.getDate()}`
  }
}

//  watch 
watch(() => trendTimeRange.value, () => {
  // 
  // Vue taskTrendOption
})

// 
const getVirtualData = (year) => {
  const data = []
  const startDate = new Date(year, 0, 1)
  const endDate = new Date()
  
  for (let time = startDate.getTime(); time <= endDate.getTime(); time += 86400000) {
    const date = new Date(time)
    const dateStr = formatDate(date, 'yyyy-MM-dd')
    const completedCount = getCompletedTasksForDate(date)
    const newCount = getNewTasksForDate(date)
    data.push([dateStr, completedCount + newCount])
  }
  
  return data
}

// 
const updateRecentActivities = () => {
  const activities = []
  const now = Date.now()
  const threeDaysAgo = now - (3 * 24 * 60 * 60 * 1000)
  
  // 
  todos.value
    .filter(task => task.completed && task.completedAt > threeDaysAgo)
    .forEach(task => {
      activities.push({
        id: `complete-${task.id}`,
        content: ` "${task.text}"`,
        time: formatDate(task.completedAt),
        type: 'success'
      })
    })
    
  // 
  todos.value
    .filter(task => task.createdAt > threeDaysAgo)
    .forEach(task => {
      activities.push({
        id: `create-${task.id}`,
        content: ` "${task.text}"`,
        time: formatDate(task.createdAt),
        type: 'primary'
      })
    })
  
  // 
  recentActivities.value = activities
    .sort((a, b) => new Date(b.time) - new Date(a.time))
    .slice(0, 5)
}

//  todos 
watch(() => todos.value, () => {
  updateRecentActivities()
}, { deep: true })

// 
const getGoalProgress = () => {
  const categories = {
    '': { total: 0, completed: 0, target: 100 },
    '': { total: 0, completed: 0, target: 80 },
    '': { total: 0, completed: 0, target: 60 },
    '': { total: 0, completed: 0, target: 50 }
  }
  
  // 
  todos.value.forEach(todo => {
    const category = todo.category || ''
    if (categories[category]) {
      categories[category].total++
      if (todo.completed) {
        categories[category].completed++
      }
    }
  })
  
  // 
  return Object.entries(categories).map(([name, data]) => ({
    value: data.total ? Math.round((data.completed / data.total) * 100) : 0,
    target: data.target,
    name,
    itemStyle: {
      color: getCategoryColor(name)
    }
  }))
}

// 
const getCategoryColor = (category) => {
  const colors = {
    '': '#409EFF',
    '': '#67C23A',
    '': '#E6A23C',
    '': '#909399'
  }
  return colors[category]
}

// 
const goalProgressOption = computed(() => ({
  tooltip: {
    trigger: 'axis',
    formatter: function(params) {
      const data = params[0]
      return `${data.name}<br/>: ${data.value}%<br/>: ${data.target}%`
    }
  },
  grid: {
    top: '10%',
    left: '3%',
    right: '15%',
    bottom: '10%',
    containLabel: true
  },
  xAxis: {
    type: 'value',
    max: 100,
    axisLabel: {
      formatter: '{value}%'
    }
  },
  yAxis: {
    type: 'category',
    data: ['', '', '', ''],
    axisLine: { show: false },
    axisTick: { show: false }
  },
  series: [
    {
      type: 'bar',
      name: '',
      data: getGoalProgress(),
      barWidth: '20px',
      label: {
        show: true,
        position: 'right',
        formatter: function(params) {
          return `${params.value}%`
        }
      },
      markLine: {
        symbol: ['none', 'none'],
        label: { show: false },
        lineStyle: { type: 'dashed' },
        data: getGoalProgress().map((item, index) => ([
          { coord: [item.target, index], lineStyle: { color: item.itemStyle.color } },
          { coord: [item.target, index + 0.5] }
        ]))
      }
    }
  ]
}))

// 
const heatmapOption = computed(() => {
  const date = new Date()
  const data = getVirtualData(date.getFullYear())
  return {
    visualMap: {
      min: 0,
      max: 10,
      calculable: true,
      orient: 'horizontal',
      left: 'center',
      bottom: 0,
      inRange: {
        color: ['#ebedf0', '#9be9a8', '#40c463', '#30a14e', '#216e39']
      }
    },
    calendar: {
      top: 30,
      left: 30,
      right: 30,
      cellSize: ['auto', 13],
      range: date.getFullYear(),
      itemStyle: {
        borderWidth: 0.5
      },
      yearLabel: { show: false }
    },
    series: {
      type: 'heatmap',
      coordinateSystem: 'calendar',
      data: data
    }
  }
})

// 
const recentActivities = ref([
  {
    id: 1,
    content: ' ""',
    time: '2024-01-20 10:30:00',
    type: 'success'
  },
  {
    id: 2,
    content: ' " Vue.js "',
    time: '2024-01-20 09:15:00',
    type: 'primary'
  },
  {
    id: 3,
    content: ' 25 ',
    time: '2024-01-20 08:45:00',
    type: 'warning'
  }
])

// 5
const recentTodos = computed(() => {
  return todos.value
    .filter(t => !t.completed)
    .sort((a, b) => (b.createdAt || 0) - (a.createdAt || 0))
    .slice(0, 5)
})

// 
const goToTodo = () => {
  router.push('/todos')
  // 
  setTimeout(() => {
    const input = document.querySelector('.add_task_input input')
    if (input) {
      input.focus()
    }
  }, 100)
}

// 
const getPriorityType = (priority) => {
  const types = {
    '': 'danger',
    '': 'warning',
    '': 'success'
  }
  return types[priority] || 'info'
}

const formatDate = (timestamp) => {
  if (!timestamp) return ''
  const date = new Date(timestamp)
  return date.toLocaleString('zh-CN', {
    year: 'numeric',
    month: '2-digit',
    day: '2-digit',
    hour: '2-digit',
    minute: '2-digit'
  })
}

const isOverdue = (date) => {
  if (!date) return false
  return new Date(date) < new Date()
}

// 
const handleStatClick = (type) => {
  switch (type) {
    case 'todo':
      router.push('/todos')  // 
      break
    case 'pomodoro':
      router.push('/pomodoro-timer')  // 
      break
    case 'completed':
      router.push({ 
        path: '/todos',
        query: { filter: 'completed' }  // 
      })
      break
    case 'overdue':
      router.push({
        path: '/todos',
        query: { filter: 'overdue' }  // 
      })
      break
  }
}

// 
onMounted(() => {
  loadTodos()
  updateRecentActivities()
  reviewStore.loadReviews()
})

// 
const reviewType = ref('daily')
const reviewDialogVisible = ref(false)
const reviewFormRef = ref(null)

const reviewForm = ref({
  type: 'daily',
  title: '',
  achievements: '',
  lessons: '',
  plans: '',
})

const reviewRules = {
  type: [{ required: true, message: '', trigger: 'change' }],
  title: [{ required: true, message: '', trigger: 'blur' }],
  achievements: [{ required: true, message: '', trigger: 'blur' }],
  lessons: [{ required: true, message: '', trigger: 'blur' }],
  plans: [{ required: true, message: '', trigger: 'blur' }],
}

// 
const recentReviews = computed(() => {
  return reviewStore.getRecentReviews(5)
})

// 
const filteredReviews = computed(() => {
  return reviewStore.getFilteredReviews(reviewType.value)
})

// 
const getReviewDialogTitle = computed(() => {
  const typeMap = {
    daily: '',
    weekly: '',
    monthly: '',
  }
  return `${typeMap[reviewForm.value.type]}`
})

// 
const openReviewDialog = () => {
  reviewForm.value = {
    type: reviewType.value,
    title: '',
    achievements: '',
    lessons: '',
    plans: '',
  }
  reviewDialogVisible.value = true
}

// 
const submitReview = async () => {
  if (!reviewFormRef.value) return
  
  await reviewFormRef.value.validate((valid) => {
    if (valid) {
      const now = new Date()
      let reviewDate = now
      
      // 
      if (!reviewForm.value.title) {
        // ...  ...
      }

      const newReview = {
        id: Date.now(),
        ...reviewForm.value,
        date: formatDate(reviewDate),
      }

      reviewStore.addReview(newReview)
      reviewDialogVisible.value = false
      reviewFormRef.value.resetFields()
      ElMessage.success('')
    }
  })
}

// 
const getReviewType = (type) => {
  const typeMap = {
    daily: 'primary',
    weekly: 'success',
    monthly: 'warning',
  }
  return typeMap[type] || 'info'
}

// 
const getReviewTagType = (type) => {
  const typeMap = {
    daily: '',
    weekly: 'success',
    monthly: 'warning',
  }
  return typeMap[type] || 'info'
}

// 
const getReviewTypeText = (type) => {
  const typeMap = {
    daily: '',
    weekly: '',
    monthly: '',
  }
  return typeMap[type] || ''
}
</script>

