import { defineStore } from 'pinia'
import { ref, computed } from 'vue'

export const useNoteStore = defineStore('note', () => {
  // 
  const notes = ref(JSON.parse(localStorage.getItem('notes') || '[]'))
  
  // 
  const pinnedNotes = computed(() => notes.value.filter(note => note.isPinned))
  const unpinnedNotes = computed(() => notes.value.filter(note => !note.isPinned))
  const archivedNotes = computed(() => notes.value.filter(note => note.isArchived))
  
  // 
  const saveToLocal = () => {
    localStorage.setItem('notes', JSON.stringify(notes.value))
  }
  
  const addNote = (note) => {
    const newNote = {
      id: Date.now().toString(),
      title: note.title || '',
      content: note.content || '',
      tags: note.tags || [],
      isPinned: note.isPinned || false,
      isArchived: false,
      createdAt: new Date().toISOString(),
      updatedAt: new Date().toISOString()
    }
    notes.value.unshift(newNote)
    saveToLocal()
    return newNote
  }
  
  const updateNote = (id, updates) => {
    const index = notes.value.findIndex(note => note.id === id)
    if (index > -1) {
      notes.value[index] = {
        ...notes.value[index],
        ...updates,
        updatedAt: new Date().toISOString()
      }
      saveToLocal()
    }
  }
  
  const deleteNote = (id) => {
    const index = notes.value.findIndex(note => note.id === id)
    if (index > -1) {
      notes.value.splice(index, 1)
      saveToLocal()
    }
  }
  
  const togglePin = (id) => {
    const note = notes.value.find(note => note.id === id)
    if (note) {
      note.isPinned = !note.isPinned
      saveToLocal()
    }
  }
  
  const toggleArchive = (id) => {
    const note = notes.value.find(note => note.id === id)
    if (note) {
      note.isArchived = !note.isArchived
      note.isPinned = false // 
      saveToLocal()
    }
  }
  
  const addTag = (noteId, tag) => {
    const note = notes.value.find(note => note.id === noteId)
    if (note) {
      //  tags 
      if (!Array.isArray(note.tags)) {
        note.tags = []
      }
      // 
      note.tags = [...new Set([...note.tags, tag])]
      saveToLocal()
      return [...note.tags]  // 
    }
    return []  // 
  }
  
  const removeTag = (noteId, tag) => {
    const note = notes.value.find(note => note.id === noteId)
    if (note) {
      note.tags = note.tags.filter(t => t !== tag)
      saveToLocal()
    }
  }

  // 
  const restoreNote = (note) => {
    notes.value.push(note)
    saveToLocal()
  }

  return {
    notes,
    pinnedNotes,
    unpinnedNotes,
    archivedNotes,
    addNote,
    updateNote,
    deleteNote,
    togglePin,
    toggleArchive,
    addTag,
    removeTag,
    restoreNote
  }
}) 