<template>
  <div class="goal-manager">
    <!--  -->
    <el-empty
      v-if="!goals.length"
      description=""
      class="empty-state"
    >
      <el-button type="primary" @click="showCreateDialog = true">
        
      </el-button>
      <div class="guide-text">
        <p> </p>
        <ul>
          <li> </li>
          <li> </li>
          <li> </li>
          <li> </li>
        </ul>
      </div>
    </el-empty>

    <!--  -->
    <template v-else>
      <!--  -->
      <div class="nav-section">
        <div class="nav-header">
          <h2></h2>
          <el-tooltip content="" placement="right">
            <div class="goal-stats">
              <el-progress
                type="circle"
                :percentage="completionRate"
                :status="completionRate >= 80 ? 'success' : 'primary'"
                :width="60"
              />
              <div class="stats-text">
                <div>: {{ goals.length }}</div>
                <div>: {{ completedGoals }}</div>
              </div>
            </div>
          </el-tooltip>
        </div>

        <el-button 
          type="primary" 
          class="create-button" 
          @click="showCreateDialog = true"
        >
          
        </el-button>

        <div class="view-switcher">
          <el-radio-group v-model="viewMode">
            <el-radio-button label="mind-map"></el-radio-button>
            <el-radio-button label="list"></el-radio-button>
            <el-radio-button label="kanban"></el-radio-button>
          </el-radio-group>
        </div>
        
        <!--  -->
        <div class="filter-section">
          <h4></h4>
          <div class="quick-filters">
            <el-tag 
              v-for="filter in quickFilters" 
              :key="filter.value"
              :type="filter.type"
              :effect="currentFilter === filter.value ? 'dark' : 'light'"
              @click="applyFilter(filter)"
              class="filter-tag"
            >
              <el-icon><component :is="filter.icon" /></el-icon>
              {{ filter.label }}
              <span class="count">({{ getFilterCount(filter.value) }})</span>
            </el-tag>
          </div>
        </div>

        <!--  -->
        <div class="help-section">
          <el-collapse>
            <el-collapse-item title="" name="1">
              <div class="help-content">
                <p>1. </p>
                <p>2. </p>
                <p>3. </p>
                <p>4. </p>
              </div>
            </el-collapse-item>
          </el-collapse>
        </div>
      </div>

      <!--  -->
      <div class="main-content">
        <!--  -->
        <div class="view-info">
          <el-alert
            v-if="viewMode === 'mind-map'"
            type="info"
            show-icon
            :closable="false"
          >
            
          </el-alert>
          <el-alert
            v-if="viewMode === 'kanban'"
            type="info"
            show-icon
            :closable="false"
          >
            
          </el-alert>
        </div>

        <!--  -->
        <component
          :is="currentViewComponent"
          :goals="filteredGoals"
          @select="handleGoalSelect"
          @update="handleGoalUpdate"
        />
      </div>
    </template>

    <!--  -->
    <el-drawer
      v-model="showDetail"
      title=""
      size="500px"
      :show-close="true"
      :with-header="true"
    >
      <template #header>
        <div class="drawer-header">
          <h3></h3>
          <el-button-group>
            <el-button size="small" @click="editGoal"></el-button>
            <el-button size="small" type="danger" @click="deleteGoal"></el-button>
          </el-button-group>
        </div>
      </template>
      <goal-detail
        v-if="selectedGoal"
        :goal="selectedGoal"
        @update="handleGoalUpdate"
      />
    </el-drawer>

    <!--  -->
    <el-dialog
      v-model="showCreateDialog"
      title=""
      width="60%"
      :before-close="handleCreateDialogClose"
    >
      <div class="dialog-header">
        <el-steps :active="createStep" finish-status="success">
          <el-step title="" />
          <el-step title="" />
          <el-step title="" />
        </el-steps>
      </div>
      <goal-creator
        :ai-enabled="true"
        @create="handleGoalCreate"
        @cancel="showCreateDialog = false"
        :current-step="createStep"
        @step-change="handleStepChange"
      />
    </el-dialog>
  </div>
</template>

<script setup>
import { ref, computed, provide, nextTick, getCurrentInstance } from 'vue'
import { message, ElMessage } from 'ant-design-vue'
import { ElMessageBox } from 'element-plus'

// 
import MindMapView from '../components/goals/GoalMindMap.vue'
import ListView from '../components/goals/GoalList.vue'
import KanbanView from '../components/goals/GoalKanban.vue'
import GoalDetail from '../components/goals/GoalDetail.vue'
import GoalCreator from '../components/goals/GoalCreator.vue'

// 
const viewMode = ref('mind-map')
const showDetail = ref(false)
const showCreateDialog = ref(false)
const goals = ref([])
const selectedGoal = ref(null)

// 
const quickFilters = [
  { label: '', value: 'all', type: '' },
  { label: '', value: 'in_progress', type: 'primary' },
  { label: '', value: 'completed', type: 'success' },
  { label: '', value: 'overdue', type: 'danger' }
]

// 
const currentFilter = ref('all')

// 
const filteredGoals = computed(() => {
  if (!currentFilter.value || currentFilter.value === 'all') {
    return goals.value
  }
  
  return goals.value.filter(goal => {
    switch (currentFilter.value) {
      case 'in_progress':
        return goal.status === 'in_progress'
      case 'completed':
        return goal.status === 'completed'
      case 'overdue': {
        const deadline = new Date(goal.deadline)
        return deadline < new Date() && goal.status !== 'completed'
      }
      default:
        return true
    }
  })
})

// 
const applyFilter = (filter) => {
  currentFilter.value = filter.value
}

// 
const handleGoalSelect = (goal) => {
  selectedGoal.value = goal
  showDetail.value = true
}

// 
const handleGoalUpdate = (updatedGoal) => {
  // 
  const index = goals.value.findIndex(g => g.id === updatedGoal.id)
  if (index !== -1) {
    goals.value[index] = updatedGoal
    message.success('')
  }
}

// 
const handleGoalCreate = (newGoal) => {
  //  goals 
  goals.value = [...goals.value, newGoal]
  showCreateDialog.value = false
  ElMessage.success('')
  
  // 
  nextTick(() => {
    if (viewMode.value === 'mind-map') {
      const mindMapComponent = getCurrentInstance().refs.mindMap
      if (mindMapComponent) {
        mindMapComponent.initMindMap()
      }
    }
  })
}

//  - 
const getStatusType = (status) => {
  const statusMap = {
    '': 'primary',
    '': 'success',
    '': 'warning',
    '': 'info'
  }
  return statusMap[status] || 'default'
}

//  - 
const getProgressStatus = (progress) => {
  if (progress >= 100) return 'success'
  if (progress >= 60) return 'primary'
  if (progress >= 30) return 'warning'
  return 'exception'
}

//  - 
const getTaskStatusType = (status) => {
  const statusMap = {
    '': 'info',
    '': 'primary',
    '': 'success',
    '': 'danger'
  }
  return statusMap[status] || 'default'
}

// 
const createNewGoal = () => {
  showCreateDialog.value = true
}

// 
provide('goalUtils', {
  getStatusType,
  getProgressStatus,
  getTaskStatusType
})

// 
provide('goalState', {
  goals,
  selectedGoal,
  createNewGoal,
  handleGoalUpdate,
  handleGoalCreate,
  handleGoalSelect
})

// 
const createStep = ref(1)
const completedGoals = computed(() => goals.value.filter(g => g.status === 'completed').length)
const completionRate = computed(() => Math.round((completedGoals.value / goals.value.length) * 100) || 0)

// 
const currentViewComponent = computed(() => {
  switch (viewMode.value) {
    case 'mind-map': return MindMapView
    case 'list': return ListView
    case 'kanban': return KanbanView
    default: return ListView
  }
})

// 
const getFilterCount = (filterValue) => {
  switch (filterValue) {
    case 'all': return goals.value.length
    case 'in_progress': return goals.value.filter(g => g.status === 'in_progress').length
    case 'completed': return completedGoals.value
    case 'overdue': return goals.value.filter(g => {
      const deadline = new Date(g.deadline)
      return deadline < new Date() && g.status !== 'completed'
    }).length
    default: return 0
  }
}

// 
const handleCreateDialogClose = (done) => {
  ElMessageBox.confirm('')
    .then(() => {
      createStep.value = 1
      done()
    })
    .catch(() => {})
}

// 
const handleStepChange = (step) => {
  createStep.value = step
}
</script>

<style scoped>
.goal-manager {
  display: flex;
  height: 100%;
  gap: 2rem;
  padding: 1rem;
}

.mind-map-section {
  flex: 1;
  display: flex;
  flex-direction: column;
  border-right: 1px solid var(--el-border-color-light);
}

.section-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  border-bottom: 1px solid var(--el-border-color-light);
}

.mind-map {
  flex: 1;
  min-height: 500px;
  height: calc(100vh - 200px);
  border: 1px solid var(--el-border-color-light);
  border-radius: 4px;
  margin: 1rem;
  position: relative;
  overflow: hidden;
}

.goal-detail-section {
  flex: 1;
  padding: 1rem;
  overflow-y: auto;
}

.detail-header {
  margin-bottom: 2rem;
}

.goal-status {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-top: 1rem;
}

.detail-section {
  margin-bottom: 2rem;
}

.detail-section h4 {
  margin-bottom: 1rem;
  color: var(--el-text-color-primary);
}

.steps-list {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.step-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem;
  border-radius: 4px;
  background-color: var(--el-bg-color-page);
}

.nav-section {
  width: 200px;
  padding: 1rem;
  border-right: 1px solid var(--el-border-color-light);
}

.view-switcher {
  margin-bottom: 1rem;
}

.quick-filters {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.main-content {
  flex: 1;
  overflow: hidden;
}

.el-tag {
  cursor: pointer;
  transition: all 0.3s;
}

.el-tag:hover {
  transform: translateX(5px);
}

.create-button {
  width: 100%;
  margin-bottom: 1rem;
}

/*  */
.empty-state {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.guide-text {
  margin-top: 2rem;
  text-align: left;
}

.guide-text ul {
  list-style: none;
  padding: 0;
}

.guide-text li {
  margin: 0.5rem 0;
}

.nav-header {
  margin-bottom: 1rem;
}

.goal-stats {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin: 1rem 0;
  padding: 1rem;
  background: var(--el-bg-color-page);
  border-radius: 8px;
}

.stats-text {
  font-size: 0.875rem;
  color: var(--el-text-color-secondary);
}

.filter-tag {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 0.5rem;
  cursor: pointer;
  transition: all 0.3s;
}

.filter-tag:hover {
  transform: translateX(5px);
}

.count {
  font-size: 0.75rem;
  opacity: 0.8;
}

.help-section {
  margin-top: auto;
  padding-top: 1rem;
}

.help-content {
  font-size: 0.875rem;
  color: var(--el-text-color-regular);
}

.view-info {
  margin-bottom: 1rem;
}

.drawer-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.dialog-header {
  margin-bottom: 2rem;
}
</style> 