<template>
  <div class="h-full flex flex-col relative">
    <!--  -->
    <div class="flex-none flex items-center justify-between px-6 py-2 border-b border-gray-100">
      <div class="flex items-center gap-3">
        <div class="w-1 h-6 bg-blue-500 rounded-full"></div>
        <h1 class="text-xl font-medium text-gray-900"> </h1>
        <span class="text-sm text-gray-400">{{ today }}</span>
      </div>
      <div class="flex items-center gap-2">
        <a-tag :color="statusCount.active > 0 ? 'processing' : 'default'">
           {{ statusCount.active }}
        </a-tag>
        <a-tag color="success">
           {{ statusCount.completed }}
        </a-tag>
      </div>
    </div>

    <!--  -->
    <div class="flex-none flex items-center gap-3 px-6 py-2 bg-white border-b border-gray-100">
      <div class="flex-1 max-w-md">
        <a-input-search
          v-model:value="searchText"
          placeholder="..."
          class="!rounded-lg search-input"
          :bordered="false"
          @search="onSearch"
        >
          <template #prefix>
            <search-outlined class="text-gray-400" />
          </template>
        </a-input-search>
      </div>
      <a-select
        v-model:value="filterStatus"
        class="!rounded-lg min-w-[120px]"
        :bordered="false"
        @change="onFilterChange"
        :options="[
          { value: 'all', label: '' },
          { value: 'active', label: ' ' },
          { value: 'completed', label: ' ' }
        ]"
      />
      <div 
        class="completed-tasks-control"
        @click="toggleCompletedTasks"
      >
        <div class="flex items-center gap-2 px-3 py-1.5 rounded-lg cursor-pointer hover:bg-gray-50">
          <div class="completed-count">
            <span class="number">{{ completedTodos.length }}</span>
            <span class="label"></span>
          </div>
          <div class="toggle-icon" :class="{ 'rotated': !showCompletedTasks }">
            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round">
              <polyline points="6 9 12 15 18 9"></polyline>
            </svg>
          </div>
        </div>
      </div>
    </div>

    <!--  -  flex-1  -->
    <div class="flex-1 px-6 overflow-hidden">
      <a-table
        :dataSource="filteredTodos"
        :columns="columns"
        :pagination="false"
        :scroll="{ y: 'calc(100vh - 340px)' }"
        :bordered="false"
        size="middle"
        class="custom-table -mx-4"
        @change="handleTableChange"
        :rowClassName="getRowClassName"
      >
        <template #bodyCell="{ column, record }">
          <!--  -->
          <template v-if="column.key === 'completed'">
            <a-checkbox
              :checked="record.completed"
              @change="e => toggleTodo(record, e.target.checked)"
              class="todo-checkbox"
            />
          </template>

          <!--  -->
          <template v-else-if="column.key === 'priority'">
            <a-dropdown :trigger="['hover']">
              <a-tag 
                :color="getPriorityColor(record.priority)"
                class="cursor-pointer"
              >
                {{ record.priority }}
              </a-tag>
              <template #overlay>
                <a-menu @click="({ key }) => changePriority(record, key)">
                  <a-menu-item key="">
                    <div class="flex items-center gap-2">
                      <div class="w-2 h-2 rounded-full bg-red-500"></div>
                      <span></span>
                    </div>
                  </a-menu-item>
                  <a-menu-item key="">
                    <div class="flex items-center gap-2">
                      <div class="w-2 h-2 rounded-full bg-yellow-500"></div>
                      <span></span>
                    </div>
                  </a-menu-item>
                  <a-menu-item key="">
                    <div class="flex items-center gap-2">
                      <div class="w-2 h-2 rounded-full bg-green-500"></div>
                      <span></span>
                    </div>
                  </a-menu-item>
                </a-menu>
              </template>
            </a-dropdown>
          </template>

          <!--  -->
          <template v-else-if="column.key === 'category'">
            <a-dropdown :trigger="['hover']">
              <a-tag 
                :color="getCategoryColor(record.category)"
                class="cursor-pointer"
              >
                {{ record.category || '' }}
              </a-tag>
              <template #overlay>
                <a-menu @click="({ key }) => changeCategory(record, key)">
                  <a-menu-item key="">
                    <div class="flex items-center gap-2">
                      <a-tag color="blue" size="small"></a-tag>
                      <span></span>
                    </div>
                  </a-menu-item>
                  <a-menu-item key="">
                    <div class="flex items-center gap-2">
                      <a-tag color="success" size="small"></a-tag>
                      <span></span>
                    </div>
                  </a-menu-item>
                  <a-menu-item key="">
                    <div class="flex items-center gap-2">
                      <a-tag color="warning" size="small"></a-tag>
                      <span></span>
                    </div>
                  </a-menu-item>
                  <a-menu-item key="">
                    <div class="flex items-center gap-2">
                      <a-tag color="default" size="small"></a-tag>
                      <span></span>
                    </div>
                  </a-menu-item>
                </a-menu>
              </template>
            </a-dropdown>
          </template>

          <!--  -->
          <template v-else-if="column.key === 'text'">
            <template v-if="editingId === record.id">
              <a-input
                v-model:value="editingText"
                @pressEnter="saveTodo"
                @blur="saveTodo"
                class="!rounded-lg !border-blue-200 focus:!border-blue-400"
                :bordered="true"
              />
            </template>
            <template v-else>
              <div class="group flex items-center relative">
                <span 
                  :class="{ 
                    'line-through text-gray-400': record.completed,
                    'in-progress-text': record.pomodoros > 0 && !record.completed 
                  }"
                  class="flex-1 truncate cursor-pointer"
                  @mouseenter="showDetails(record, $event)"
                  @mouseleave="hideDetails"
                >
                  {{ record.text }}
                </span>

                <!--  -->
                <div class="action-buttons flex items-center gap-2 transition-opacity">
                  <a-button 
                    v-if="!record.completed"
                    type="text" 
                    @click="startTask(record)"
                    class="action-btn !px-2 hover:!bg-blue-50 hover:!text-blue-500"
                    :title="record.pomodoros > 0 ? '' : ''"
                  >
                    <template #icon>
                      <clock-circle-outlined />
                    </template>
                  </a-button>
                  <a-button 
                    type="text" 
                    @click="startEdit(record)"
                    class="action-btn !px-2 hover:!bg-gray-100"
                    title=""
                  >
                    <template #icon><edit-outlined /></template>
                  </a-button>
                  <a-button 
                    type="text" 
                    @click="deleteTodo(record)"
                    class="action-btn !px-2 hover:!bg-red-50 hover:!text-red-500"
                    title=""
                  >
                    <template #icon><delete-outlined /></template>
                  </a-button>
                </div>

                <!--  -->
                <div 
                  v-if="isHovering && hoveredTodo?.id === record.id" 
                  class="tooltip"
                >
                  <div class="tooltip-content">
                    <p class="font-medium mb-2">{{ record.text }}</p>
                    <p class="text-sm text-gray-500">{{ record.priority }}</p>
                    <p class="text-sm text-gray-500">{{ record.category }}</p>
                    <p class="text-sm text-gray-500">{{ formatDate(record.createdAt) }}</p>
                    <p class="text-sm text-gray-500" v-if="record.dueDate">
                      {{ record.completed ? '' : '' }}
                      <span :class="{ 'text-red-500': !record.completed && isOverdue(record.dueDate) }">
                        {{ formatDate(record.dueDate) }}
                      </span>
                    </p>
                  </div>
                </div>
              </div>
            </template>
          </template>

          <!--  -->
          <template v-else-if="column.key === 'dueDate'">
            <div class="flex items-center gap-2">
              <span 
                :class="{ 
                  'text-red-500': !record.completed && isOverdue(record.dueDate),
                  'text-gray-400': record.completed
                }"
                class="cursor-pointer hover:text-blue-500"
                @click="openDatePicker(record)"
              >
                {{ record.completed ? '' + formatDate(record.dueDate) : formatDate(record.dueDate) || '' }}
              </span>
              <a-button
                v-if="record.dueDate"
                type="text"
                class="action-btn !px-1"
                @click="clearDueDate(record)"
                title=""
              >
                <template #icon>
                  <close-outlined />
                </template>
              </a-button>
            </div>
          </template>
          <!--  -->
          <template v-else-if="column.key === 'pomodoros'">
            <span class="pomodoro-count"> x {{ record.pomodoros || 0 }}</span>
          </template>

          <!--  -->
          <template v-else-if="column.key === 'createdAt'">
            <span>{{ formatDate(record.createdAt) }}</span>
          </template>
        </template>
      </a-table>
    </div>

    <!--  -->
    <div class="flex-none px-6 py-4">
      <a-input
        v-model:value="newTodo"
        placeholder="..."
        class="add-task-input"
        :bordered="true"
        @keyup.enter="addTodo"
      >
        <!--  -->
        <template #prefix>
          <div class="flex items-center gap-2">
            <div class="flex items-center gap-2">
              <!--  -  -->
              <a-dropdown :trigger="['hover']" placement="bottomLeft">
                <a-tag 
                  v-if="newTodoCategory" 
                  :color="getCategoryColor(newTodoCategory)"
                  size="small"
                  class="hover:cursor-pointer"
                >
                  {{ getCategoryShortName(newTodoCategory) }}
                </a-tag>
                <template #overlay>
                  <a-menu @click="({ key }) => setCategory(key)">
                    <a-menu-item key="">
                      <a-tag color="blue" size="small"></a-tag>
                      <span class="ml-2"></span>
                    </a-menu-item>
                    <a-menu-item key="">
                      <a-tag color="success" size="small"></a-tag>
                      <span class="ml-2"></span>
                    </a-menu-item>
                    <a-menu-item key="">
                      <a-tag color="warning" size="small"></a-tag>
                      <span class="ml-2"></span>
                    </a-menu-item>
                    <a-menu-item key="">
                      <a-tag color="default" size="small"></a-tag>
                      <span class="ml-2"></span>
                    </a-menu-item>
                  </a-menu>
                </template>
              </a-dropdown>

              <!--  -  -->
              <a-dropdown :trigger="['hover']" placement="bottomLeft">
                <a-tag 
                  v-if="newTodoPriority" 
                  :color="getPriorityColor(newTodoPriority)"
                  size="small"
                  class="hover:cursor-pointer"
                >
                  {{ newTodoPriority }}
                </a-tag>
                <template #overlay>
                  <a-menu @click="({ key }) => setPriority(key)">
                    <a-menu-item key="">
                      <a-tag color="error" size="small"></a-tag>
                      <span class="ml-2"></span>
                    </a-menu-item>
                    <a-menu-item key="">
                      <a-tag color="warning" size="small"></a-tag>
                      <span class="ml-2"></span>
                    </a-menu-item>
                    <a-menu-item key="">
                      <a-tag color="success" size="small"></a-tag>
                      <span class="ml-2"></span>
                    </a-menu-item>
                  </a-menu>
                </template>
              </a-dropdown>
            </div>
            <a-dropdown>
              <plus-outlined class="text-gray-400 cursor-pointer hover:text-blue-500 text-lg" />
              <template #overlay>
                <a-menu>
                  <a-menu-item-group title="">
                    <a-menu-item key="work" @click="setCategory('')">
                      <div class="flex items-center gap-2">
                        <a-tag color="blue" size="small"></a-tag>
                        <span></span>
                      </div>
                    </a-menu-item>
                    <a-menu-item key="study" @click="setCategory('')">
                      <div class="flex items-center gap-2">
                        <a-tag color="success" size="small"></a-tag>
                        <span></span>
                      </div>
                    </a-menu-item>
                    <a-menu-item key="life" @click="setCategory('')">
                      <div class="flex items-center gap-2">
                        <a-tag color="warning" size="small"></a-tag>
                        <span></span>
                      </div>
                    </a-menu-item>
                    <a-menu-item key="other" @click="setCategory('')">
                      <div class="flex items-center gap-2">
                        <a-tag color="default" size="small"></a-tag>
                        <span></span>
                      </div>
                    </a-menu-item>
                  </a-menu-item-group>
                  <a-menu-divider />
                  <a-menu-item-group title="">
                    <a-menu-item key="high" @click="setPriority('')">
                      <div class="flex items-center gap-2">
                        <div class="w-2 h-2 rounded-full bg-red-500"></div>
                        <span></span>
                      </div>
                    </a-menu-item>
                    <a-menu-item key="medium" @click="setPriority('')">
                      <div class="flex items-center gap-2">
                        <div class="w-2 h-2 rounded-full bg-yellow-500"></div>
                        <span></span>
                      </div>
                    </a-menu-item>
                    <a-menu-item key="low" @click="setPriority('')">
                      <div class="flex items-center gap-2">
                        <div class="w-2 h-2 rounded-full bg-green-500"></div>
                        <span></span>
                      </div>
                    </a-menu-item>
                  </a-menu-item-group>
                  <a-menu-divider />
                  <a-menu-item key="date" @click="setDueDate">
                     
                  </a-menu-item>
                </a-menu>
              </template>
            </a-dropdown>
          </div>
        </template>

        <!--  -->
        <template #suffix>
          <enter-outlined 
            v-if="newTodo.trim()"
            class="text-gray-300 text-lg"
            title=""
          />
        </template>
      </a-input>
    </div>

    <!--  -->
    <a-modal
      v-model:visible="datePickerVisible"
      title=""
      :footer="null"
    >
      <a-date-picker
        v-model:value="tempDueDate"
        show-time
        format="YYYY-MM-DD HH:mm"
        @ok="handleDateOk"
        @change="handleDateChange"
      />
    </a-modal>

    <!--  -->
    <div v-if="showConfetti" class="confetti-container">
      <canvas ref="confettiCanvas" class="confetti-canvas"></canvas>
    </div>
    
    <!--  -->
    <el-dialog
      v-model="showConfirmationModal"
      title=""
      width="360px"
      :show-close="false"
      :close-on-click-modal="false"
      class="completion-dialog"
    >
      <div class="completion-content">
        <div class="completion-emoji"></div>
        <div class="completion-message">
          
          <div class="task-name">{{ completedTask?.text }}</div>
          <div class="completion-time">
            {{ formatDuration(Date.now() - completedTask?.createdAt) }}
          </div>
        </div>
      </div>
      <template #footer>
        <div class="dialog-footer">
          <el-button type="primary" @click="handleTaskComplete"></el-button>
        </div>
      </template>
    </el-dialog>
  </div>
</template>

<script setup>
import { ref, computed, onMounted, onUnmounted, h, nextTick } from 'vue'
import { message } from 'ant-design-vue'
import { useRouter } from 'vue-router'
import { 
  PlusOutlined, 
  EditOutlined, 
  DeleteOutlined,
  SearchOutlined,
  EnterOutlined,  // 
  ClockCircleOutlined,
  CloseOutlined  // 
} from '@ant-design/icons-vue'
import dayjs from 'dayjs'
import confetti from 'canvas-confetti'

//  router
const router = useRouter()

// 
const todos = ref([])
const newTodo = ref('')
const editingId = ref(null)
const editingText = ref('')
const searchText = ref('')
const filterStatus = ref('all')
const newTodoPriority = ref(null) // 
const newDueDate = ref(null)
const datePickerVisible = ref(false)
const tempDueDate = ref(null)
const editingDueDate = ref(null)  // 
const newTodoCategory = ref(null) // 
const detailsVisible = ref(false)
const selectedTodoDetails = ref('')
const isHovering = ref(false)
const hoveredTodo = ref(null)

// 
const deleteHistory = ref([])

// 
const sortState = ref({
  columnKey: 'priority',  // 
  order: 'descend'         // 
})

// 
const showConfetti = ref(false)
const confettiCanvas = ref(null)
const showConfirmationModal = ref(false)
const completedTask = ref(null)
const showCompletedTasks = ref(false)

// 
const columns = [
  {
    title: '',
    key: 'completed',
    width: 60,
    sorter: (a, b) => Number(a.completed) - Number(b.completed),
  },
  {
    title: '',
    key: 'text',
    ellipsis: true,
    sorter: (a, b) => a.text.localeCompare(b.text),
  },
  {
    title: '',
    key: 'priority',
    width: 100,
    sorter: (a, b) => {
      const priorityWeight = { '': 3, '': 2, '': 1 }
      return priorityWeight[a.priority] - priorityWeight[b.priority]
    },
  },
  {
    title: '',
    key: 'category',
    width: 120,
    sorter: (a, b) => (a.category || '').localeCompare(b.category || ''),
  },
  {
    title: '',
    key: 'createdAt',
    width: 180,
    defaultSortOrder: 'descend', // 
    sorter: (a, b) => a.createdAt - b.createdAt,
  },
  {
    title: '',
    key: 'dueDate',
    width: 180,
    sorter: (a, b) => {
      if (!a.dueDate && !b.dueDate) return 0
      if (!a.dueDate) return 1
      if (!b.dueDate) return -1
      return a.dueDate - b.dueDate
    },
  },
  {
    title: '',
    key: 'pomodoros',
    width: 100,
    sorter: (a, b) => (a.pomodoros || 0) - (b.pomodoros || 0),
  }
]

// 
const today = computed(() => {
  return new Date().toLocaleDateString('zh-CN', {
    month: 'long',
    day: 'numeric',
    weekday: 'long'
  })
})

// 
const filteredTodos = computed(() => {
  const list = todos.value.filter(todo => {
    if (!showCompletedTasks.value && todo.completed) {
      return false // 
    }
    return true // 
  })
  const priorityWeight = { '': 3, '': 2, '': 1 }
  const localSortState = sortState.value
  if (localSortState && localSortState.order && localSortState.columnKey) {
    if (localSortState.columnKey === 'priority') {
      // 
      list.sort((a, b) => {
        const diff = priorityWeight[b.priority] - priorityWeight[a.priority]
        if (diff !== 0) return diff
        return b.createdAt - a.createdAt
      })
    } else if (localSortState.columnKey === 'createdAt') {
      list.sort((a, b) =>
        localSortState.order === 'descend'
          ? b.createdAt - a.createdAt
          : a.createdAt - b.createdAt
      )
    } else if (localSortState.columnKey === 'text') {
      list.sort((a, b) =>
        localSortState.order === 'descend'
          ? b.text.localeCompare(a.text)
          : a.text.localeCompare(b.text)
      )
    } else if (localSortState.columnKey === 'dueDate') {
      list.sort((a, b) =>
        localSortState.order === 'descend'
          ? (b.dueDate || 0) - (a.dueDate || 0)
          : (a.dueDate || 0) - (b.dueDate || 0)
      )
    } else if (localSortState.columnKey === 'completed') {
      list.sort((a, b) =>
        localSortState.order === 'descend'
          ? Number(b.completed) - Number(a.completed)
          : Number(a.completed) - Number(b.completed)
      )
    } else if (localSortState.columnKey === 'pomodoros') {
      list.sort((a, b) =>
        localSortState.order === 'descend'
          ? (b.pomodoros || 0) - (a.pomodoros || 0)
          : (a.pomodoros || 0) - (b.pomodoros || 0)
      )
    } else {
      // 
      list.sort((a, b) => {
        const diff = priorityWeight[b.priority] - priorityWeight[a.priority]
        if (diff !== 0) return diff
        return b.createdAt - a.createdAt
      })
    }
  } else {
    // 
    list.sort((a, b) => {
      const diff = priorityWeight[b.priority] - priorityWeight[a.priority]
      if (diff !== 0) return diff
      return b.createdAt - a.createdAt
    })
  }
  return list
})

const statusCount = computed(() => ({
  active: todos.value.filter(t => !t.completed).length,
  completed: todos.value.filter(t => t.completed).length
}))

const getPriorityColor = (priority) => {
  const colors = { '': 'error', '': 'warning', '': 'success' }
  return colors[priority] || 'default'
}

const formatDate = (timestamp) => {
  if (!timestamp) return '';
  const date = new Date(timestamp);
  if (isNaN(date.getTime())) return '';
  
  return date.toLocaleString('zh-CN', {
    year: 'numeric',
    month: '2-digit',
    day: '2-digit',
    hour: '2-digit',
    minute: '2-digit'
  });
}

const isOverdue = (date) => {
  if (!date) return false
  return new Date(date) < new Date()
}

// 
const addTodo = () => {
  if (!newTodo.value.trim()) {
    message.warning('')
    return
  }

  const todo = {
    id: Date.now(),
    text: newTodo.value.trim(),
    completed: false,
    category: newTodoCategory.value || '',
    priority: newTodoPriority.value || '',
    dueDate: newDueDate.value,
    pomodoros: 0,
    createdAt: Date.now()
  }

  todos.value.push(todo)
  saveTodosToStorage()
  newTodo.value = ''
  newTodoPriority.value = null
  newTodoCategory.value = null
  newDueDate.value = null
  message.success('')
}

const deleteTodo = (todo) => {
  const index = todos.value.findIndex(t => t.id === todo.id)
  if (index !== -1) {
    // 
    deleteHistory.value.push({
      todo: { ...todo },
      index,
      timestamp: Date.now()
    })
    
    // 
    todos.value.splice(index, 1)
    saveTodosToStorage()
    
    // 3
    message.info({
      content: h('div', {
        class: 'undo-message'
      }, [
        h('span', ' '),
        h('a', {
          style: {
            color: '#1890ff',
            cursor: 'pointer',
            marginLeft: '4px'
          },
          onClick: () => undoDelete(deleteHistory.value[deleteHistory.value.length - 1])
        }, 'Ctrl+Z'),
        h('span', {
          style: {
            color: '#999',
            fontSize: '12px',
            marginLeft: '4px'
          }
        }, '(3)')
      ]),
      duration: 3,  // 3
      class: 'custom-message'
    })
  }
}

const startEdit = (record) => {
  editingId.value = record.id
  editingText.value = record.text
}

const saveTodo = () => {
  if (editingId.value) {
    const todo = todos.value.find(t => t.id === editingId.value)
    if (todo) {
      todo.text = editingText.value
      saveTodosToStorage()
    }
    editingId.value = null
    editingText.value = ''
  }
}

const onSearch = () => {
  // 
  loadTodosFromStorage()
}

const onFilterChange = () => {
  // 
  loadTodosFromStorage()
}

const saveTodosToStorage = () => {
  localStorage.setItem('todos', JSON.stringify(todos.value))
}

const loadTodosFromStorage = () => {
  const stored = localStorage.getItem('todos')
  if (stored) {
    todos.value = JSON.parse(stored)
  }
}

const setPriority = (priority) => {
  newTodoPriority.value = priority
  message.success(`${priority}`)
}

const setDueDate = () => {
  datePickerVisible.value = true
}

// 
const handleDateChange = (date) => {
  tempDueDate.value = date
}

const handleDateOk = () => {
  if (editingDueDate.value) {
    // 
    editingDueDate.value.dueDate = tempDueDate.value ? tempDueDate.value.valueOf() : null
    saveTodosToStorage()
    datePickerVisible.value = false
    editingDueDate.value = null
    tempDueDate.value = null
    message.success('')
  }
}

// 
const getPriorityDot = (priority) => {
  switch (priority) {
    case '':
      return 'bg-red-500'
    case '':
      return 'bg-yellow-500'
    case '':
      return 'bg-green-500'
    default:
      return 'bg-gray-300'
  }
}

const toggleTodo = async (todo, checked) => {
  if (checked) {
    // 
    completedTask.value = todo
    showConfirmationModal.value = true
    // 
    showFullScreenConfetti()
  } else {
    // 
    todo.completed = checked
    saveTodosToStorage()
  }
}

// 
const handleTaskComplete = () => {
  if (completedTask.value) {
    completedTask.value.completed = true
    completedTask.value.dueDate = Date.now()
    saveTodosToStorage()
    
    // 
    showConfirmationModal.value = false
    completedTask.value = null
  }
}

// 
const showFullScreenConfetti = async () => {
  showConfetti.value = true
  await nextTick()
  
  const duration = 3000
  const animationEnd = Date.now() + duration
  const defaults = { startVelocity: 30, spread: 360, ticks: 60, zIndex: 9999 }

  function randomInRange(min, max) {
    return Math.random() * (max - min) + min
  }

  const interval = setInterval(function() {
    const timeLeft = animationEnd - Date.now()

    if (timeLeft <= 0) {
      clearInterval(interval)
      showConfetti.value = false
      return
    }

    const particleCount = 50 * (timeLeft / duration)
    
    // 
    confetti({
      ...defaults,
      particleCount,
      origin: { x: randomInRange(0.1, 0.3), y: Math.random() - 0.2 },
      colors: ['#ff0000', '#00ff00', '#0000ff', '#ffff00', '#ff00ff']
    })
    confetti({
      ...defaults,
      particleCount,
      origin: { x: randomInRange(0.7, 0.9), y: Math.random() - 0.2 },
      colors: ['#ff0000', '#00ff00', '#0000ff', '#ffff00', '#ff00ff']
    })
  }, 250)
}

// 
const formatDuration = (ms) => {
  const seconds = Math.floor(ms / 1000)
  const minutes = Math.floor(seconds / 60)
  const hours = Math.floor(minutes / 60)
  const days = Math.floor(hours / 24)

  if (days > 0) {
    return `${days}${hours % 24}`
  } else if (hours > 0) {
    return `${hours}${minutes % 60}`
  } else if (minutes > 0) {
    return `${minutes}`
  } else {
    return `${seconds}`
  }
}

// 
const startTask = (task) => {
  router.push({
    path: '/pomodoro-timer',
    query: { taskId: task.id }
  })
}

// 3
const undoDelete = (deleteRecord) => {
  if (!deleteRecord) return
  
  // 3
  if (Date.now() - deleteRecord.timestamp > 3000) {  // 3000
    message.error('')
    return
  }
  
  // 
  todos.value.splice(deleteRecord.index, 0, deleteRecord.todo)
  saveTodosToStorage()
  
  // 
  const index = deleteHistory.value.findIndex(h => h.timestamp === deleteRecord.timestamp)
  if (index !== -1) {
    deleteHistory.value.splice(index, 1)
  }
  
  message.success('')
}

// 3
const cleanupDeleteHistory = () => {
  const now = Date.now()
  deleteHistory.value = deleteHistory.value.filter(
    record => now - record.timestamp <= 3000  // 3000
  )
}

// 
onMounted(() => {
  window.addEventListener('keydown', handleKeyDown)
})

onUnmounted(() => {
  window.removeEventListener('keydown', handleKeyDown)
})

const handleKeyDown = (e) => {
  //  Ctrl+Z
  if (e.ctrlKey && e.key.toLowerCase() === 'z') {
    e.preventDefault()
    const lastDelete = deleteHistory.value[deleteHistory.value.length - 1]
    if (lastDelete) {
      undoDelete(lastDelete)
    }
  }
}

// 
onMounted(() => {
  const cleanup = setInterval(cleanupDeleteHistory, 1000)
  onUnmounted(() => clearInterval(cleanup))
})

// 
const handleTableChange = (pagination, filters, sorter) => {
  sortState.value = {
    columnKey: sorter.field,
    order: sorter.order
  }
}

// 
const changePriority = (todo, priority) => {
  const updatedTodo = {
    ...todo,
    priority
  }
  todos.value = todos.value.map(t => 
    t.id === todo.id ? updatedTodo : t
  )
  saveTodosToStorage()
}

// 
const openDatePicker = (todo) => {
  editingDueDate.value = todo
  tempDueDate.value = todo.dueDate ? dayjs(todo.dueDate) : null
  datePickerVisible.value = true
}

// 
const clearDueDate = (todo) => {
  todo.dueDate = null
  saveTodosToStorage()
  message.success('')
}

//  script 
const setCategory = (category) => {
  newTodoCategory.value = category
  message.success(`${category}`)
}

//  Tag 
const getCategoryColor = (category) => {
  const colors = {
    '': 'blue',
    '': 'success',
    '': 'warning',
    '': 'default'  //  default 
  }
  return colors[category] || 'default'
}

// 
const getCategoryShortName = (category) => {
  const shortNames = {
    '': '',
    '': '',
    '': '',
    '': ''
  }
  return shortNames[category] || ''
}

// 
const changeCategory = (todo, category) => {
  const updatedTodo = {
    ...todo,
    category
  }
  todos.value = todos.value.map(t => 
    t.id === todo.id ? updatedTodo : t
  )
  saveTodosToStorage()
  message.success(`${category}`)
}

// 
const showDetails = (record, event) => {
  hoveredTodo.value = record
  isHovering.value = true
  // 
  const rect = event.target.getBoundingClientRect()
  const tooltipEl = document.querySelector('.tooltip')
  if (tooltipEl) {
    tooltipEl.style.top = `${rect.top - 10}px`
    tooltipEl.style.left = `${rect.left + (rect.width / 2)}px`
  }
}

const hideDetails = () => {
  hoveredTodo.value = null
  isHovering.value = false
}

// 
const completedTodos = computed(() => {
  return todos.value.filter(todo => todo.completed)
})

// 
const toggleCompletedTasks = () => {
  showCompletedTasks.value = !showCompletedTasks.value
  
  // 
  const emoji = showCompletedTasks.value ? '' : ''
  message.success({
    content: h('div', [
      h('span', showCompletedTasks.value ? ' ' : ' '),
      h('span', emoji)
    ]),
    duration: 2
  })
}

// 
const getRowClassName = (record) => {
  return {
    'completed-row': record.completed,
    'completed-row-hidden': record.completed && !showCompletedTasks.value
  }
}

// 
loadTodosFromStorage()
</script>

