<template>
  <template v-for="node in menuNodes" :key="node.id">
    <!--  sub-menu -->
    <el-sub-menu 
      v-if="node.children && node.children.length && node.visible" 
      :index="node.path"
    >
      <template #title>
        <el-icon><component :is="node.icon" /></el-icon>
        <span>{{ node.title }}</span>
      </template>
      <!--  -->
      <RenderMenuNodes :menu-nodes="node.children" />
    </el-sub-menu>

    <!--  menu-item -->
    <el-menu-item 
      v-else-if="node.visible"
      :index="node.path"
      @click="navigateTo(node)"
    >
      <el-icon><component :is="node.icon" /></el-icon>
      <span>{{ node.title }}</span>
    </el-menu-item>
  </template>
</template>

<script setup>
import { useRouter } from 'vue-router'
import {
  HomeFilled,
  List,
  Timer,
  Aim,
  Share,
  Memo,
  Collection,
  Calendar,
  ChatDotRound,
  EditPen,
} from '@element-plus/icons-vue'

const router = useRouter()

defineProps({
  menuNodes: {
    type: Array,
    required: true
  }
})

// 
const navigateTo = (node) => {
  const path = node.path
  
  // 
  if (path.startsWith('@')) {
    // @ 
    window.open(path.slice(1), '_blank')
  } else if (path.startsWith('#')) {
    // #  iframe 
    router.push({
      path: '/iframe-view',
      query: { 
        url: path.slice(1),
        title: node.title
      }
    })
  } else if (path.startsWith('/web-view')) {
    //  web-view 
    router.push(path)
  } else if (path.startsWith('http://') || path.startsWith('https://')) {
    //  URL web-view
    router.push({
      path: '/web-view',
      query: { 
        url: path,
        title: node.title
      }
    })
  } else {
    // 
    router.push(path)
  }
}
</script> 