<template>
  <div class="reviews-container">
    <div class="page-header">
      <h2></h2>
      <el-button type="primary" @click="openReviewDialog">
        <el-icon><plus /></el-icon>
      </el-button>
    </div>

    <!--  -->
    <div class="filter-section">
      <el-radio-group v-model="reviewType" size="large">
        <el-radio-button label="all"></el-radio-button>
        <el-radio-button label="daily"></el-radio-button>
        <el-radio-button label="weekly"></el-radio-button>
        <el-radio-button label="monthly"></el-radio-button>
      </el-radio-group>
    </div>

    <!--  -->
    <el-timeline>
      <el-timeline-item
        v-for="review in filteredReviews"
        :key="review.id"
        :timestamp="getReviewTimeRange(review)"
        :type="getReviewType(review.type)"
      >
        <el-card class="review-item">
          <template #header>
            <div class="review-item-header">
              <el-tag :type="getReviewTagType(review.type)">
                {{ getReviewTypeText(review) }}
              </el-tag>
              <span class="review-title">{{ review.title }}</span>
              <div class="review-actions">
                <el-button type="primary" link @click="editReview(review)">
                  
                </el-button>
                <el-button type="danger" link @click="deleteReview(review)">
                  
                </el-button>
              </div>
            </div>
          </template>
          <div class="review-content">
            <div v-if="review.achievements" class="review-section">
              <h4> </h4>
              <p>{{ review.achievements }}</p>
            </div>
            <div v-if="review.lessons" class="review-section">
              <h4> </h4>
              <p>{{ review.lessons }}</p>
            </div>
            <div v-if="review.plans" class="review-section">
              <h4> </h4>
              <p>{{ review.plans }}</p>
            </div>
          </div>
        </el-card>
      </el-timeline-item>
    </el-timeline>

    <!--  -->
    <el-dialog
      v-model="reviewDialogVisible"
      :title="dialogTitle"
      width="60%"
    >
      <el-form :model="reviewForm" ref="reviewFormRef" :rules="reviewRules">
        <el-form-item label="" prop="type">
          <el-select v-model="reviewForm.type">
            <el-option label="" value="daily" />
            <el-option label="" value="weekly" />
            <el-option label="" value="monthly" />
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="title">
          <el-input v-model="reviewForm.title" placeholder="" />
        </el-form-item>
        <el-form-item label="" prop="achievements">
          <el-input
            v-model="reviewForm.achievements"
            type="textarea"
            :rows="3"
            placeholder=""
          />
        </el-form-item>
        <el-form-item label="" prop="lessons">
          <el-input
            v-model="reviewForm.lessons"
            type="textarea"
            :rows="3"
            placeholder=""
          />
        </el-form-item>
        <el-form-item label="" prop="plans">
          <el-input
            v-model="reviewForm.plans"
            type="textarea"
            :rows="3"
            placeholder=""
          />
        </el-form-item>
      </el-form>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="reviewDialogVisible = false"></el-button>
          <el-button type="primary" @click="submitReview">
            
          </el-button>
        </span>
      </template>
    </el-dialog>
  </div>
</template>

<script setup>
import { ref, computed, onMounted, watch } from 'vue'
import { Plus } from '@element-plus/icons-vue'
import { ElMessage, ElMessageBox } from 'element-plus'
import { useReviewStore } from '@/stores/review'

const reviewStore = useReviewStore()

// 
const reviewType = ref('all')
const reviewDialogVisible = ref(false)
const reviewFormRef = ref(null)
const isEditing = ref(false)
const currentReviewId = ref(null)

// 
const reviewForm = ref({
  type: 'daily',
  title: '',
  achievements: '',
  lessons: '',
  plans: '',
})

// 
const reviewRules = {
  type: [{ required: true, message: '', trigger: 'change' }],
  title: [{ required: true, message: '', trigger: 'blur' }],
  achievements: [{ required: true, message: '', trigger: 'blur' }],
  lessons: [{ required: true, message: '', trigger: 'blur' }],
  plans: [{ required: true, message: '', trigger: 'blur' }],
}

// 
const dialogTitle = computed(() => {
  return isEditing.value ? '' : ''
})

// 
const filteredReviews = computed(() => {
  return reviewStore.getFilteredReviews(reviewType.value)
})

// 
watch(() => reviewForm.value.type, (newType) => {
  if (!isEditing.value) {  // 
    const now = new Date()
    switch (newType) {
      case 'daily':
        reviewForm.value.title = formatDateWithYear(now)
        break
      case 'weekly': {
        const weekStart = new Date(now)
        weekStart.setDate(now.getDate() - now.getDay() + 1)
        const firstDayOfYear = new Date(now.getFullYear(), 0, 1)
        const weekNumber = Math.ceil((((now - firstDayOfYear) / 86400000) + firstDayOfYear.getDay() + 1) / 7)
        reviewForm.value.title = `${now.getFullYear()}${now.getMonth() + 1} ${weekNumber}`
        break
      }
      case 'monthly':
        reviewForm.value.title = `${now.getFullYear()}${now.getMonth() + 1}`
        break
    }
  }
})

// 
const formatDateWithYear = (date) => {
  return date.toLocaleDateString('zh-CN', {
    year: 'numeric',
    month: '2-digit',
    day: '2-digit'
  }).replace(/\//g, '').replace(/-/g, '') + ''
}

// 
const openReviewDialog = () => {
  isEditing.value = false
  currentReviewId.value = null
  const now = new Date()
  const type = reviewType.value === 'all' ? 'daily' : reviewType.value
  
  // 
  let title = ''
  switch (type) {
    case 'daily':
      title = formatDateWithYear(now)
      break
    case 'weekly': {
      const weekStart = new Date(now)
      weekStart.setDate(now.getDate() - now.getDay() + 1)
      const firstDayOfYear = new Date(now.getFullYear(), 0, 1)
      const weekNumber = Math.ceil((((now - firstDayOfYear) / 86400000) + firstDayOfYear.getDay() + 1) / 7)
      title = `${now.getFullYear()}${now.getMonth() + 1} ${weekNumber}`
      break
    }
    case 'monthly':
      title = `${now.getFullYear()}${now.getMonth() + 1}`
      break
  }

  reviewForm.value = {
    type,
    title,
    achievements: '',
    lessons: '',
    plans: '',
  }
  reviewDialogVisible.value = true
}

// 
const editReview = (review) => {
  isEditing.value = true
  currentReviewId.value = review.id
  reviewForm.value = { ...review }
  reviewDialogVisible.value = true
}

// 
const deleteReview = async (review) => {
  try {
    await ElMessageBox.confirm(
      '',
      '',
      {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning',
      }
    )
    reviewStore.deleteReview(review.id)
    ElMessage.success('')
  } catch {
    // 
  }
}

// 
const submitReview = async () => {
  if (!reviewFormRef.value) return
  
  await reviewFormRef.value.validate((valid) => {
    if (valid) {
      const now = new Date()
      let reviewDate = now
      
      // 
      switch (reviewForm.value.type) {
        case 'weekly':
          reviewDate = new Date(now)
          reviewDate.setDate(now.getDate() - now.getDay() + 1)
          break
        case 'monthly':
          reviewDate = new Date(now.getFullYear(), now.getMonth(), 1)
          break
      }

      if (isEditing.value) {
        reviewStore.updateReview(currentReviewId.value, {
          ...reviewForm.value,
        })
      } else {
        reviewStore.addReview({
          id: Date.now(),
          ...reviewForm.value,
          date: formatDate(reviewDate),
        })
      }
      
      reviewDialogVisible.value = false
      reviewFormRef.value.resetFields()
      ElMessage.success(isEditing.value ? '' : '')
    }
  })
}

// 
const getReviewType = (type) => {
  const typeMap = {
    daily: 'primary',
    weekly: 'success',
    monthly: 'warning',
  }
  return typeMap[type] || 'info'
}

const getReviewTagType = (type) => {
  const typeMap = {
    daily: '',
    weekly: 'success',
    monthly: 'warning',
  }
  return typeMap[type] || 'info'
}

const getReviewTypeText = (review) => {
  const date = new Date(review.date)
  
  switch (review.type) {
    case 'daily':
      return ''  // 
    case 'weekly': {
      const weekStart = new Date(date)
      weekStart.setDate(date.getDate() - date.getDay() + 1)
      const weekEnd = new Date(date)
      weekEnd.setDate(date.getDate() + (7 - date.getDay()))
      const firstDayOfYear = new Date(date.getFullYear(), 0, 1)
      const weekNumber = Math.ceil((((date - firstDayOfYear) / 86400000) + firstDayOfYear.getDay() + 1) / 7)
      return ` ${weekNumber}`
    }
    case 'monthly': {
      const monthNames = ['', '', '', '', '', '', '', '', '', '', '', '']
      return ` ${monthNames[date.getMonth()]}`
    }
    default:
      return ''
  }
}

//  script setup 
const getReviewTimeRange = (review) => {
  const date = new Date(review.date)
  
  switch (review.type) {
    case 'daily':
      return formatDate(date)  // 
    case 'weekly': {
      const weekStart = new Date(date)
      weekStart.setDate(date.getDate() - date.getDay() + 1)
      const weekEnd = new Date(date)
      weekEnd.setDate(date.getDate() + (7 - date.getDay()))
      return `${formatDate(weekStart)} ~ ${formatDate(weekEnd)}`
    }
    case 'monthly': {
      const monthStart = new Date(date.getFullYear(), date.getMonth(), 1)
      const monthEnd = new Date(date.getFullYear(), date.getMonth() + 1, 0)
      return `${formatDate(monthStart)} ~ ${formatDate(monthEnd)}`
    }
    default:
      return review.date
  }
}

// 
const formatDate = (date) => {
  return date.toLocaleDateString('zh-CN', {
    year: 'numeric',
    month: '2-digit',
    day: '2-digit'
  }).replace(/\//g, '-')
}

// 
onMounted(() => {
  reviewStore.loadReviews()
})
</script>

