<template>
  <div class="goal-list">
    <el-table :data="goals" style="width: 100%">
      <el-table-column prop="title" label="" />
      <el-table-column prop="progress" label="" width="200">
        <template #default="{ row }">
          <el-progress :percentage="row.progress" />
        </template>
      </el-table-column>
      <el-table-column prop="status" label="" width="120">
        <template #default="{ row }">
          <el-tag :type="getStatusType(row.status)">{{ row.status }}</el-tag>
        </template>
      </el-table-column>
      <el-table-column prop="deadline" label="" width="180" />
      <el-table-column label="" width="150">
        <template #default="{ row }">
          <el-button @click="handleSelect(row)" type="primary" link>
            
          </el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script setup>
import { inject, defineProps, defineEmits } from 'vue'

defineProps({
  goals: {
    type: Array,
    required: true
  }
})

const emit = defineEmits(['select'])

// 
const { getStatusType } = inject('goalUtils')

const handleSelect = (goal) => {
  emit('select', goal)
}
</script>

<style scoped>
.goal-list {
  padding: 1rem;
}
</style> 