<template>
  <el-container class="report-container">
    <el-header class="report-header">
      <div class="report-header-left">
        <el-radio-group v-model="reportType" @change="handleReportTypeChange">
          <el-radio-button label="daily"></el-radio-button>
          <el-radio-button label="weekly"></el-radio-button>
          <el-radio-button label="monthly"></el-radio-button>
          <el-radio-button label="quarterly"></el-radio-button>
          <el-radio-button label="annual"></el-radio-button>
        </el-radio-group>
      </div>
      <div class="report-header-right">
        <el-date-picker
          v-model="reportDate"
          type="date"
          :placeholder="datePlaceholder"
          :picker-options="datePickerOptions"
          @change="handleDateChange"
          class="report-date-picker"
        />
        <div class="report-date-info">
          {{ formattedDateInfo }}
        </div>
      </div>
    </el-header>

    <el-main class="report-main">
      <el-card class="report-card">
        <el-form ref="form" :model="form" label-width="120px">
          <el-form-item label="">
            <el-input type="textarea" v-model="form.description" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="form.status" placeholder="">
              <el-option label="" value="completed"></el-option>
              <el-option label="" value="inprogress"></el-option>
              <el-option label="" value="pending"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="onSubmit"></el-button>
          </el-form-item>
        </el-form>
      </el-card>

      <el-card class="report-input-card">
        <el-form :inline="true" :model="reportInput" class="report-input-form">
          <el-form-item label="">
            <el-input v-model="reportInput.done" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="reportInput.doing" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="reportInput.todo" placeholder=""></el-input>
          </el-form-item>
        </el-form>
      </el-card>

      <el-table :data="reports" style="width: 100%" class="report-table">
        <el-table-column prop="date" label="" width="180"></el-table-column>
        <el-table-column prop="description" label=""></el-table-column>
        <el-table-column prop="status" label="" width="120">
          <template #default="{ row }">
            <el-tag
              :type="
                row.status === 'completed'
                  ? 'success'
                  : row.status === 'inprogress'
                  ? 'warning'
                  : 'info'
              "
              size="small"
            >
              {{
                row.status === 'completed'
                  ? ''
                  : row.status === 'inprogress'
                  ? ''
                  : ''
              }}
            </el-tag>
          </template>
        </el-table-column>
      </el-table>
    </el-main>
  </el-container>
</template>

<script>
import { ref, computed } from 'vue';
import dayjs from 'dayjs';
import 'dayjs/locale/zh-cn';
import weekOfYear from 'dayjs/plugin/weekOfYear';
import quarterOfYear from 'dayjs/plugin/quarterOfYear';

dayjs.extend(weekOfYear);
dayjs.extend(quarterOfYear);
dayjs.locale('zh-cn');

export default {
  setup() {
    const reportType = ref('daily');
    const reportDate = ref(new Date());
    const form = ref({
      description: '',
      status: '',
    });
    const reports = ref([]);
    const reportInput = ref({
      done: '',
      doing: '',
      todo: '',
    });

    const datePlaceholder = computed(() => {
      if (reportType.value === 'daily') return '';
      if (reportType.value === 'weekly') return '';
      if (reportType.value === 'monthly') return '';
      if (reportType.value === 'quarterly') return '';
      return '';
    });

    const datePickerOptions = computed(() => {
      if (reportType.value === 'weekly') {
        return {
          firstDayOfWeek: 1,
          format: 'yyyy W',
          pickerType: 'week',
        };
      }
      if (reportType.value === 'monthly') {
        return {
          format: 'yyyy-MM',
          pickerType: 'month',
        };
      }
      if (reportType.value === 'quarterly') {
        return {
          format: 'yyyy Q',
          pickerType: 'quarter',
        };
      }
      if (reportType.value === 'annual') {
        return {
          format: 'yyyy',
          pickerType: 'year',
        };
      }
      return {};
    });

    const formattedDateInfo = computed(() => {
      const date = dayjs(reportDate.value);
      const weekNumber = date.week();
      const month = date.format('MM');
      const quarter = date.quarter();
      const year = date.format('YYYY');
      const day = date.format('YYYY-MM-DD');

      return `${day}${month}${weekNumber}${quarter}${year}`;
    });

    const handleReportTypeChange = () => {
      reportDate.value = new Date();
    };

    const handleDateChange = (date) => {
      if (date) {
        form.value.date = date;
      }
    };

    const onSubmit = () => {
      reports.value.push({ ...form.value, date: reportDate.value });
      form.value = { description: '', status: '' };
    };

    return {
      reportType,
      reportDate,
      form,
      reports,
      reportInput,
      datePlaceholder,
      datePickerOptions,
      formattedDateInfo,
      handleReportTypeChange,
      handleDateChange,
      onSubmit,
    };
  },
};
</script>

<style scoped>
.report-container {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.report-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 2rem;
}

.report-header-left {
  flex: 1;
}

.report-header-right {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.report-date-picker {
  width: 200px;
}

.report-date-info {
  font-size: 1rem;
  color: #666;
}

.report-main {
  padding: 2rem;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.report-card {
  width: 100%;
}

.report-table {
  width: 100%;
}

.report-input-card {
  width: 100%;
}

.report-input-form {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
}

.report-input-form .el-form-item {
  margin-bottom: 0;
}
</style>
  