<template>
  <div class="calendar-container">
    <a-calendar v-model:value="currentDate">
      <!--  -->
      <template #headerRender="{ value, type, onChange, onTypeChange }">
        <div class="calendar-header">
          <div class="calendar-header-left">
            <!--  -->
            <a-select
              size="small"
              :value="value.month()"
              style="width: 80px; margin-right: 8px;"
              @change="(selectedMonth) => {
                const newValue = value.clone().month(selectedMonth)
                onChange(newValue)
              }"
            >
              <a-select-option v-for="i in 12" :key="i-1" :value="i-1">
                {{ i }}
              </a-select-option>
            </a-select>
            <!--  -->
            <a-select
              size="small"
              :value="value.year()"
              style="width: 100px;"
              @change="(selectedYear) => {
                const newValue = value.clone().year(selectedYear)
                onChange(newValue)
              }"
            >
              <a-select-option v-for="i in 10" :key="startYear + i" :value="startYear + i">
                {{ startYear + i }}
              </a-select-option>
            </a-select>
          </div>
          <div class="calendar-header-right">
            <!--  -->
            <a-radio-group
              size="small"
              :value="type"
              @change="e => onTypeChange(e.target.value)"
              style="margin-right: 8px;"
            >
              <a-radio-button value="month"></a-radio-button>
              <a-radio-button value="year"></a-radio-button>
            </a-radio-group>
            <!--  -->
            <a-button
              type="primary"
              size="small"
              @click="() => {
                const now = dayjs()
                onChange(now)
                currentDate = now
              }"
            >
              
            </a-button>
          </div>
        </div>
      </template>

      <template #dateCellRender="{ current }">
        <div 
          class="date-cell"
          @contextmenu.prevent="handleContextMenu($event, current)"
        >
          <!--  -->
          <div class="date-info">
            <!--  -->
            <span v-if="isHoliday(current)" class="holiday-mark"></span>
            <!--  -->
            <span class="solar-date" :class="{ 'is-holiday': isHoliday(current) }">
              {{ current.date() }}
            </span>
            <!--  -->
            <span class="lunar-date">{{ getLunarDate(current) }}</span>
          </div>
          <!--  -->
          <div class="todo-list">
            <div
              v-for="item in getTodosByDate(current)"
              :key="item.id"
              class="todo-item"
              :class="[
                item.priority, 
                { 
                  completed: item.completed,
                  'due-date': isDueDate(current, item),
                  'create-date': isCreateDate(current, item)
                }
              ]"
            >
              <span class="todo-tag" v-if="isDueDate(current, item)"></span>
              <span class="todo-tag" v-if="isCreateDate(current, item)"></span>
              {{ item.text }}
            </div>
          </div>
        </div>
      </template>
    </a-calendar>

    <!--  -->
    <div v-if="showContextMenu" :style="contextMenuStyle" class="context-menu">
      <div class="menu-item" @click="handleAddTodo">
        <plus-outlined />
        <span></span>
      </div>
      <div 
        v-if="todoStore.getTodosByDate(selectedDate).length"
        class="menu-item" 
        @click="handleViewTodos"
      >
        <file-outlined />
        <span></span>
      </div>
      <div 
        v-if="todoStore.getTodosByDate(selectedDate).length"
        class="menu-item" 
        @click="handleDeleteTodo"
      >
        <delete-outlined />
        <span></span>
      </div>
    </div>

    <!--  -->
    <a-modal
      v-model:open="dialogVisible"
      :title="dragMode ? '' : ''"
      @ok="saveTodo"
      @cancel="dialogVisible = false"
    >
      <a-form :model="todoForm" layout="vertical">
        <a-form-item label="" required>
          <a-input v-model:value="todoForm.title" placeholder="" />
        </a-form-item>
        
        <a-form-item label="">
          <a-textarea
            v-model:value="todoForm.description"
            :rows="3"
            placeholder=""
          />
        </a-form-item>
        
        <a-form-item label="">
          <a-select v-model:value="todoForm.priority">
            <a-select-option value=""></a-select-option>
            <a-select-option value=""></a-select-option>
            <a-select-option value=""></a-select-option>
          </a-select>
        </a-form-item>

        <a-form-item label="">
          <a-select v-model:value="todoForm.category">
            <a-select-option value=""></a-select-option>
            <a-select-option value=""></a-select-option>
            <a-select-option value=""></a-select-option>
            <a-select-option value=""></a-select-option>
          </a-select>
        </a-form-item>
      </a-form>
    </a-modal>
  </div>
</template>

<script setup>
import { ref, computed } from 'vue'
import { message, Modal } from 'ant-design-vue'
import { 
  PlusOutlined, 
  FileOutlined, 
  DeleteOutlined 
} from '@ant-design/icons-vue'
import { useRouter } from 'vue-router'
import dayjs from 'dayjs'
import { Lunar } from 'lunar-javascript'
import { useTodoStore } from '@/stores/todo'

const router = useRouter()
const todoStore = useTodoStore()

const currentDate = ref(dayjs())
const dialogVisible = ref(false)
const showContextMenu = ref(false)
const contextMenuPosition = ref({ x: 0, y: 0 })
const selectedDate = ref(null)
const dragMode = ref(false)
const selectedDates = ref([])

const todoForm = ref({
  title: '',
  date: null,
  description: '',
  priority: '',
  category: '',
  dueDate: null
})

// 5
const startYear = dayjs().year() - 5

// 
const getLunarDate = (date) => {
  try {
    const lunar = Lunar.fromDate(date.toDate())
    
    // 
    const festivals = lunar.getFestivals()
    if (festivals.length > 0) {
      return festivals[0]
    }
    
    // 
    const jieQi = lunar.getJieQi()
    if (jieQi) {
      return jieQi
    }
    
    // 
    if (lunar.getDay() === 1) {
      return lunar.getMonthInChinese() + ''
    }
    
    // 
    return lunar.getDayInChinese()
  } catch (error) {
    console.error('getLunarDate error:', error)
    return ''
  }
}

// 
const getTodosByDate = (date) => {
  return todoStore.getTodosByDate(date)
}

// 
const formatDateRange = (dates) => {
  if (!dates.length) return ''
  return dates
    .sort()
    .map(date => dayjs(date).format('YYYYMMDD'))
    .join('  ')
}

// 
const contextMenuStyle = computed(() => ({
  left: `${contextMenuPosition.value.x}px`,
  top: `${contextMenuPosition.value.y}px`
}))

// 
const handleContextMenu = (e, date) => {
  e.preventDefault()
  showContextMenu.value = true
  contextMenuPosition.value = {
    x: Math.min(e.clientX, window.innerWidth - 200),
    y: Math.min(e.clientY, window.innerHeight - 150)
  }
  selectedDate.value = date
  dragMode.value = false
  selectedDates.value = [date]
}

// 
const handleAddTodo = () => {
  todoForm.value = {
    title: '',
    description: '',
    priority: '',
    category: '',
    date: dayjs(selectedDate.value).format('YYYY-MM-DD'),
    dueDate: null
  }
  dialogVisible.value = true
  showContextMenu.value = false
}

// 
const handleViewTodos = () => {
  router.push('/todos')
  showContextMenu.value = false
}

// 
const handleDeleteTodo = () => {
  Modal.confirm({
    title: '',
    content: '',
    onOk: () => {
      todoStore.deleteTodosByDate(selectedDate.value)
      message.success('')
      showContextMenu.value = false
    }
  })
}

// 
const saveTodo = () => {
  if (!todoForm.value.title) {
    message.warning('')
    return
  }

  todoStore.addTodo(todoForm.value)
  message.success('')
  dialogVisible.value = false
}

// 
const resetForm = () => {
  todoForm.value = {
    title: '',
    date: null,
    description: '',
    priority: '',
    category: '',
    dueDate: null
  }
  selectedDates.value = []
}

// 
const handleClickOutside = (e) => {
  if (showContextMenu.value && !e.target.closest('.context-menu')) {
    showContextMenu.value = false
  }
}

// 
window.addEventListener('click', handleClickOutside)

// 
const isHoliday = (date) => {
  // 
  const isWeekend = date.day() === 0 || date.day() === 6
  
  // 
  const lunar = Lunar.fromDate(date.toDate())
  const festivals = lunar.getFestivals()
  
  // 
  const legalHolidays = [
    '',
    '',
    '',
    '',
    '',
    '',
    ''
  ]
  
  // 
  const isLegalHoliday = festivals.some(festival => 
    legalHolidays.some(holiday => festival.includes(holiday))
  )
  
  return isWeekend || isLegalHoliday
}

// 
const isDueDate = (date, todo) => {
  if (!todo.dueDate) return false
  return dayjs(todo.dueDate).format('YYYY-MM-DD') === dayjs(date).format('YYYY-MM-DD')
}

// 
const isCreateDate = (date, todo) => {
  if (!todo.date) return false
  return todo.date === dayjs(date).format('YYYY-MM-DD')
}
</script>

<style scoped>
.calendar-container {
  padding: 20px;
  background: #fff;
  border-radius: 4px;
}

.date-cell {
  position: relative;
  min-height: 80px;
  display: flex;
  flex-direction: column;
}

/*  */
.date-info {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 4px 0;
  min-height: 48px; /*  */
}

/*  */
.holiday-mark {
  position: absolute;
  top: 4px;
  left: 4px;
  font-size: 12px;
  color: #f5222d;
  font-weight: bold;
}

/*  */
.solar-date {
  font-size: 16px;
  font-weight: 500;
  line-height: 1.5;
  color: rgba(0, 0, 0, 0.85);
}

/*  */
.solar-date.is-holiday {
  color: #f5222d;
  font-weight: bold;
}

/*  */
.lunar-date {
  font-size: 12px;
  line-height: 1.5;
  color: rgba(0, 0, 0, 0.45);
  margin-top: 2px;
}

/*  */
.todo-list {
  flex: 1;
  margin-top: 4px;
  overflow-y: auto;
}

.todo-item {
  padding: 2px 4px;
  margin: 2px 0;
  font-size: 12px;
  border-radius: 2px;
  background: #f0f5ff;
  color: #1890ff;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  display: flex;
  align-items: center;
  gap: 4px;
}

/*  */
.todo-item.due-date {
  background: #fff2e8;
  color: #fa541c;
  border-left: 2px solid #fa541c;
}

/*  */
.todo-item.create-date {
  background: #f6ffed;
  color: #52c41a;
  border-left: 2px solid #52c41a;
}

/*  */
.todo-tag {
  font-size: 10px;
  padding: 0 4px;
  border-radius: 2px;
  background: rgba(0, 0, 0, 0.1);
  color: inherit;
}

/*  */
.todo-item. {
  background: #fff1f0;
  color: #f5222d;
}

.todo-item. {
  background: #fff7e6;
  color: #fa8c16;
}

.todo-item. {
  background: #f6ffed;
  color: #52c41a;
}

/*  */
.todo-item.completed {
  text-decoration: line-through;
  opacity: 0.6;
}

/*  */
.todo-item.completed.due-date,
.todo-item.completed.create-date {
  opacity: 0.4;
}

/*  */
.todo-list {
  max-height: 80px;
  overflow-y: auto;
  padding: 2px;
}

/*  */
.todo-list::-webkit-scrollbar {
  width: 4px;
}

.todo-list::-webkit-scrollbar-track {
  background: transparent;
}

.todo-list::-webkit-scrollbar-thumb {
  background: rgba(0, 0, 0, 0.1);
  border-radius: 2px;
}

.context-menu {
  position: fixed;
  background: white;
  border: 1px solid #f0f0f0;
  border-radius: 2px;
  padding: 4px 0;
  box-shadow: 0 3px 6px -4px rgba(0,0,0,.12);
  z-index: 1050;
  min-width: 160px;
}

.menu-item {
  padding: 5px 12px;
  cursor: pointer;
  display: flex;
  align-items: center;
  transition: all 0.3s;
}

.menu-item:hover {
  background: #f5f5f5;
}

.menu-item .anticon {
  margin-right: 8px;
}

.selected-dates {
  color: rgba(0, 0, 0, 0.45);
}

/*  */
:deep(.ant-picker-calendar-date-today) .solar-date {
  color: #1890ff;
  font-weight: bold;
}

:deep(.ant-picker-calendar-date-today) .solar-date.is-holiday {
  color: #f5222d;
}

:deep(.ant-picker-calendar-date-today) .lunar-date {
  color: #1890ff;
}

/*  */
:deep(.ant-picker-calendar-date-selected) .date-info {
  background: #e6f7ff;
  border-radius: 2px;
}

/*  */
.calendar-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 0;
}

.calendar-header-left,
.calendar-header-right {
  display: flex;
  align-items: center;
  gap: 8px;
}

/*  */
:deep(.ant-select-dropdown) {
  z-index: 1100;
}
</style> 