<template>
  <div class="menu-manager">
    <div class="menu-header">
      <h3></h3>
      <div class="header-actions">
        <el-button type="primary" @click="handleAddRootMenu">
          <el-icon><plus /></el-icon>
        </el-button>
      </div>
    </div>

    <div class="menu-content">
      <!--  -->
      <div class="menu-tree">
        <el-tree
          ref="treeRef"
          :data="menuList"
          node-key="id"
          :props="{ label: 'title' }"
          draggable
          :allow-drop="handleAllowDrop"
          @node-drop="handleDrop"
          default-expand-all
        >
          <template #default="{ node, data }">
            <div class="tree-node">
              <div class="node-content">
                <el-icon v-if="!data.editing"><component :is="data.icon" /></el-icon>
                <template v-if="data.editing">
                  <el-input
                    v-model="data.title"
                    size="small"
                    class="edit-input"
                    @blur="handleSave(data)"
                    @keyup.enter="handleSave(data)"
                  />
                </template>
                <span v-else class="label">{{ node.label }}</span>
              </div>
              <div class="node-actions">
                <el-button-group>
                  <el-button
                    type="primary"
                    size="small"
                    text
                    @click.stop="handleAddChild(data)"
                  >
                    <el-icon><plus /></el-icon>
                  </el-button>
                  <el-button
                    type="primary"
                    size="small"
                    text
                    @click.stop="handleEdit(data)"
                  >
                    <el-icon><edit /></el-icon>
                  </el-button>
                  <el-button
                    type="danger"
                    size="small"
                    text
                    @click.stop="handleDelete(node, data)"
                  >
                    <el-icon><delete /></el-icon>
                  </el-button>
                </el-button-group>
              </div>
            </div>
          </template>
        </el-tree>
      </div>

      <!--  -->
      <div class="menu-form" v-if="currentMenu">
        <el-form :model="currentMenu" label-width="80px">
          <el-form-item label="">
            <el-input v-model="currentMenu.title" />
          </el-form-item>
          
          <el-form-item label="">
            <el-input 
              v-model="currentMenu.path"
              placeholder="URL"
            />
          </el-form-item>
          
          <el-form-item label="">
            <el-select v-model="currentMenu.icon" class="icon-select">
              <el-option
                v-for="icon in iconList"
                :key="icon"
                :label="icon"
                :value="icon"
              >
                <el-icon><component :is="icon" /></el-icon>
                <span style="margin-left: 8px">{{ icon }}</span>
              </el-option>
            </el-select>
          </el-form-item>
          
          <el-form-item label="">
            <el-switch v-model="currentMenu.visible" />
          </el-form-item>
          
          <el-form-item>
            <el-button type="primary" @click="handleSaveForm"></el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted } from 'vue'
import { ElMessage, ElMessageBox } from 'element-plus'
import {
  Plus,
  Edit,
  Delete,
  HomeFilled,
  List,
  Timer,
  Aim,
  Share,
  Memo,
  Collection,
  Calendar,
  ChatDotRound,
  EditPen,
} from '@element-plus/icons-vue'

const treeRef = ref(null)
const menuList = ref([])
const currentMenu = ref(null)

// 
const iconList = [
  'HomeFilled',
  'List',
  'Timer',
  'Aim',
  'Share',
  'Memo',
  'Collection',
  'Calendar',
  'ChatDotRound',
  'EditPen',
]

// 
const defaultMenus = [
  {
    id: 1,
    title: '',
    path: '/dashboard',
    icon: 'HomeFilled',
    visible: true,
    children: []
  },
  {
    id: 2,
    title: '',
    path: '/tasks',
    icon: 'List',
    visible: true,
    children: [
      {
        id: 21,
        title: '',
        path: '/todos',
        icon: 'List',
        visible: true,
        children: []
      },
      {
        id: 22,
        title: '',
        path: '/goals',
        icon: 'Aim',
        visible: true,
        children: []
      }
    ]
  }
]

onMounted(() => {
  loadMenus()
})

// 
const loadMenus = () => {
  const savedMenus = localStorage.getItem('menu-config')
  if (savedMenus) {
    menuList.value = JSON.parse(savedMenus)
  } else {
    menuList.value = defaultMenus
    saveMenus()
  }
}

// 
const handleAddRootMenu = () => {
  const newMenu = {
    id: Date.now(),
    title: '',
    path: '',
    icon: 'Menu',
    visible: true,
    children: [],
    editing: true
  }
  menuList.value.push(newMenu)
  currentMenu.value = newMenu
}

// 
const handleAddChild = (data) => {
  const newChild = {
    id: Date.now(),
    title: '',
    path: '',
    icon: 'Menu',
    visible: true,
    children: [],
    editing: true
  }
  if (!data.children) {
    data.children = []
  }
  data.children.push(newChild)
  currentMenu.value = newChild
  saveMenus()
}

// 
const handleEdit = (data) => {
  currentMenu.value = data
}

// 
const handleDelete = (node, data) => {
  ElMessageBox.confirm(
    '',
    '',
    {
      confirmButtonText: '',
      cancelButtonText: '',
      type: 'warning'
    }
  ).then(() => {
    const parent = node.parent
    const children = parent.data.children || parent.data
    const index = children.findIndex(d => d.id === data.id)
    children.splice(index, 1)
    saveMenus()
    ElMessage.success('')
    if (currentMenu.value?.id === data.id) {
      currentMenu.value = null
    }
  })
}

// 
const handleSave = (data) => {
  data.editing = false
  saveMenus()
}

// 
const handleSaveForm = () => {
  saveMenus()
  ElMessage.success('')
}

// 
const handleAllowDrop = (draggingNode, dropNode, type) => {
  // 
  if (draggingNode.data.id === dropNode.data.id) {
    return false
  }
  return true
}

// 
const handleDrop = (draggingNode, dropNode, type) => {
  saveMenus()
}

// 
const saveMenus = () => {
  localStorage.setItem('menu-config', JSON.stringify(menuList.value))
}
</script>

<style scoped>
.menu-manager {
  padding: 20px;
}

.menu-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.menu-content {
  display: flex;
  gap: 20px;
  min-height: 500px;
}

.menu-tree {
  flex: 0 0 300px;
  border: 1px solid var(--el-border-color-light);
  border-radius: 4px;
  padding: 10px;
}

.menu-form {
  flex: 1;
  padding: 20px;
  border: 1px solid var(--el-border-color-light);
  border-radius: 4px;
}

.tree-node {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 4px 0;
}

.node-content {
  display: flex;
  align-items: center;
  gap: 8px;
}

.edit-input {
  width: 150px;
}

.label {
  font-size: 14px;
}

.node-actions {
  opacity: 0;
  transition: opacity 0.2s;
}

.tree-node:hover .node-actions {
  opacity: 1;
}

.icon-select {
  width: 100%;
}

:deep(.el-tree-node__content) {
  height: 32px;
}

:deep(.el-tree-node.is-drop-inner > .el-tree-node__content) {
  background-color: var(--el-color-primary-light-9);
}
</style> 