<template>
  <div class="mind-map-container">
    <div class="mind-map" ref="mindMapContainer"></div>
  </div>
</template>

<script setup>
import { ref, onMounted, onBeforeUnmount, watch, defineProps, defineEmits } from 'vue'
import MindMap from 'simple-mind-map'

const props = defineProps({
  goals: {
    type: Array,
    required: true,
    default: () => []
  }
})

const emit = defineEmits(['node-click'])
const mindMapContainer = ref(null)
let mindMap = null

// 
const formatDate = (date) => {
  if (!date) return ''
  const d = new Date(date)
  const year = d.getFullYear()
  const month = String(d.getMonth() + 1).padStart(2, '0')
  const day = String(d.getDate()).padStart(2, '0')
  return `${year}-${month}-${day}`
}

// 
const transformGoalsToMindMap = (goals) => {
  console.log(':', goals)
  const currentYear = new Date().getFullYear()
  const result = {
    data: {
      text: `${currentYear}`,
      expand: true,
      isActive: false,
      style: {
        fontSize: '18px',
        fontWeight: 'bold',
        color: '#409EFF'
      },
      children: (goals || []).map(goal => ({
        text: goal.title || '',
        id: goal.id,
        expand: true,
        isActive: false,
        // 
        style: {
          fontSize: '16px',
          color: getPriorityColor(goal.priority),
          border: `2px solid ${getPriorityColor(goal.priority)}`
        },
        // 
        tooltip: `
          : ${goal.priority}
          : ${formatDate(goal.startDate)}
          : ${formatDate(goal.deadline)}
          : ${goal.progress}%
        `,
        children: (goal.steps || []).map(step => ({
          text: step.title || '',
          id: `${goal.id}-step-${step.id}`,
          expand: true,
          isActive: false,
          style: {
            color: step.completed ? '#67C23A' : '#909399',
            fontSize: '14px'
          },
          // 
          tooltip: `
            : ${step.criteria}
            : ${formatDate(step.deadline)}
            : ${step.weight}%
          `
        }))
      }))
    }
  }
  console.log(':', result)
  return result
}

// 
const getPriorityColor = (priority) => {
  const colors = {
    high: '#F56C6C',
    medium: '#E6A23C',
    low: '#67C23A'
  }
  return colors[priority] || '#909399'
}

// 
const adjustView = () => {
  try {
    if (mindMap) {
      mindMap.command.execute('resetLayout')
      mindMap.render()
      // 
      const viewData = mindMap.view
      if (viewData) {
        const { clientWidth, clientHeight } = mindMapContainer.value
        const scale = Math.min(
          clientWidth / (viewData.width || 800),
          clientHeight / (viewData.height || 600)
        )
        mindMap.view.scale = Math.min(Math.max(0.5, scale), 2)
        mindMap.render()
      }
    }
  } catch (error) {
    console.error('Adjust view error:', error)
  }
}

// 
const initMindMap = () => {
  console.log(', :', mindMapContainer.value)
  if (!mindMapContainer.value) return

  try {
    const data = transformGoalsToMindMap(props.goals)
    console.log(':', data)
    
    mindMap = new MindMap({
      el: mindMapContainer.value,
      data: data,
      // 
      layout: 'circle',
      //  direction 
      theme: {
        template: 'classic',
        config: {
          // 
          backgroundColor: '#fff',
          lineColor: '#91d5ff',
          lineWidth: 2,
          fontSize: 14,
          fontFamily: 'Arial',
          color: '#333',
          // 
          lineStyle: 'curve',
          // 
          nodeStyle: {
            borderRadius: '50%',
            border: '2px solid #91d5ff',
            padding: '8px 16px'
          }
        }
      },
      width: mindMapContainer.value.clientWidth || 800,
      height: mindMapContainer.value.clientHeight || 600,
      // 
      xGap: 60,
      yGap: 60,
      mousewheelAction: 'zoom',
      enableFreeDrag: true,
      textOverflow: 'ellipsis',
      // 
      enableAnimation: true,
      // /
      expandAnimation: {
        enable: true,
        duration: 300
      }
    })

    // 
    mindMap.on('node_click', (node) => {
      try {
        if (!node || !node.data) return
        const goalId = node.data.id
        if (!goalId) return
        const goal = props.goals.find(g => g.id === goalId)
        if (goal) {
          emit('node-click', goal)
        }
      } catch (error) {
        console.error('Node click error:', error)
      }
    })

    //  DOM 
    setTimeout(() => {
      mindMap.render()
      adjustView()
    }, 0)
  } catch (error) {
    console.error('Mind map initialization error:', error)
  }
}

// 
const cleanupMindMap = () => {
  try {
    if (mindMap) {
      mindMap.off('node_click') // 
      mindMap.destroy()
      mindMap = null
    }
  } catch (error) {
    console.error('Mind map cleanup error:', error)
  }
}

// 
const handleResize = () => {
  if (mindMap && mindMapContainer.value) {
    const { clientWidth, clientHeight } = mindMapContainer.value
    mindMap.resize(clientWidth, clientHeight)
    adjustView()
  }
}

onMounted(() => {
  initMindMap()
  window.addEventListener('resize', handleResize)
})

// goals
watch(() => props.goals, (newGoals) => {
  try {
    if (mindMap) {
      const data = transformGoalsToMindMap(newGoals)
      mindMap.setData(data)
      // 
      setTimeout(() => {
        mindMap.render()
        adjustView()
      }, 0)
    }
  } catch (error) {
    console.error('Mind map update error:', error)
  }
}, { deep: true })

//  onBeforeUnmount  onUnmounted
onBeforeUnmount(() => {
  window.removeEventListener('resize', handleResize)
  cleanupMindMap()
})
</script>

<style scoped>
.mind-map-container {
  width: 100%;
  height: 100%;
  min-height: 600px;
  position: relative;
  overflow: hidden;
  background: linear-gradient(to bottom right, #f0f2f5, #ffffff);
}

.mind-map {
  width: 100%;
  height: 100%;
  min-height: 600px;
  border: 1px solid #e8e8e8;
  border-radius: 8px;
  background: #fff;
  box-shadow: 0 2px 12px 0 rgba(0,0,0,0.1);
}

/*  */
@media screen and (max-width: 768px) {
  .mind-map-container,
  .mind-map {
    min-height: 400px;
  }
}
</style> 