<template>
  <div class="mindmap-container">
    <!--  -->
    <div class="toolbar">
      <el-button-group>
        <el-button @click="createNewMap">
          <el-icon><Plus /></el-icon>
        </el-button>
        <el-button @click="saveCurrentMap">
          <el-icon><Save /></el-icon>
        </el-button>
        <el-button @click="exportMap">
          <el-icon><Download /></el-icon>
        </el-button>
      </el-button-group>

      <el-button-group>
        <el-button @click="zoomIn">
          <el-icon><ZoomIn /></el-icon>
        </el-button>
        <el-button @click="zoomOut">
          <el-icon><ZoomOut /></el-icon>
        </el-button>
        <el-button @click="resetZoom">
          <el-icon><FullScreen /></el-icon>
        </el-button>
      </el-button-group>

      <el-button-group>
        <el-button @click="undo" :disabled="!canUndo">
          <el-icon><Back /></el-icon>
        </el-button>
        <el-button @click="redo" :disabled="!canRedo">
          <el-icon><Right /></el-icon>
        </el-button>
      </el-button-group>

      <el-select v-model="currentTheme" placeholder="">
        <el-option
          v-for="theme in themes"
          :key="theme.value"
          :label="theme.label"
          :value="theme.value"
        />
      </el-select>
    </div>

    <!--  -->
    <div class="sidebar">
      <div class="map-list-header">
        <h3></h3>
        <el-input
          v-model="searchQuery"
          placeholder="..."
          prefix-icon="Search"
        />
      </div>
      
      <div class="map-list">
        <div
          v-for="map in filteredMaps"
          :key="map.id"
          class="map-item"
          :class="{ active: currentMap?.id === map.id }"
          @click="loadMap(map)"
        >
          <div class="map-info">
            <h4>{{ map.title }}</h4>
            <p>{{ formatDate(map.updatedAt) }}</p>
          </div>
          <el-dropdown trigger="click" @command="handleMapCommand($event, map)">
            <el-button type="text">
              <el-icon><More /></el-icon>
            </el-button>
            <template #dropdown>
              <el-dropdown-menu>
                <el-dropdown-item command="rename"></el-dropdown-item>
                <el-dropdown-item command="duplicate"></el-dropdown-item>
                <el-dropdown-item command="delete" divided></el-dropdown-item>
              </el-dropdown-menu>
            </template>
          </el-dropdown>
        </div>
      </div>
    </div>

    <!--  -->
    <div class="main-content">
      <template v-if="maps.length === 0">
        <div class="empty-state">
          <el-empty description="">
            <el-button type="primary" @click="createNewMap">
              
            </el-button>
          </el-empty>
        </div>
      </template>
      <template v-else>
        <div class="mindmap-wrapper" ref="mindMapContainer"></div>
      </template>
    </div>

    <!--  -->
    <el-dialog
      v-model="showCreateDialog"
      title=""
      width="30%"
    >
      <el-form :model="newMapForm" label-width="80px">
        <el-form-item label="">
          <el-input v-model="newMapForm.title" placeholder="" />
        </el-form-item>
        <el-form-item label="">
          <el-input
            v-model="newMapForm.description"
            type="textarea"
            placeholder=""
          />
        </el-form-item>
      </el-form>
      <template #footer>
        <el-button @click="showCreateDialog = false"></el-button>
        <el-button type="primary" @click="handleCreateMap"></el-button>
      </template>
    </el-dialog>
  </div>
</template>

<script setup>
import { ref, onMounted, onBeforeUnmount, computed, nextTick } from 'vue'
import MindMap from 'simple-mind-map'
import { ElMessage, ElMessageBox } from 'element-plus'
import {
  Plus, Save, Download, ZoomIn, ZoomOut,
  FullScreen, Back, Right, More, Search
} from '@element-plus/icons-vue'

// 
const mindMapContainer = ref(null)
const mindMap = ref(null)
const currentMap = ref(null)
const maps = ref([])
const searchQuery = ref('')
const showCreateDialog = ref(false)
const currentTheme = ref('default')
const canUndo = ref(false)
const canRedo = ref(false)

// 
const newMapForm = ref({
  title: '',
  description: ''
})

// 
const themes = [
  { label: '', value: 'default' },
  { label: '', value: 'fresh' },
  { label: '', value: 'dark' },
  { label: '', value: 'business' }
]

// 
const filteredMaps = computed(() => {
  if (!searchQuery.value) return maps.value
  return maps.value.filter(map => 
    map.title.toLowerCase().includes(searchQuery.value.toLowerCase())
  )
})

// 
let saveTimeout = null

// 
const loadMap = (map) => {
  currentMap.value = map
  if (mindMap.value && map.content) {
    // 
    const data = map.content
    
    // 
    if (mindMapContainer.value) {
      // 
      mindMap.value.destroy()
      
      // 
      mindMap.value = new MindMap({
        el: mindMapContainer.value,
        data: data,
        width: mindMapContainer.value.clientWidth,
        height: mindMapContainer.value.clientHeight,
        layout: 'mindMap',
        draggable: true,
        keyboard: true,
        contextMenu: true,
        theme: 'classic',
        themeConfig: {
          nodeMargin: 20,
          lineWidth: 2,
          lineColor: '#666'
        },
        viewportOptions: {
          padding: 50
        }
      })
      
      // 
      mindMap.value.on('data_change', () => {
        // 
        if (saveTimeout) clearTimeout(saveTimeout)
        saveTimeout = setTimeout(() => {
          saveCurrentMap()
        }, 1000)
      })
    }
    mindMap.value.render()
  }
}

// 
const createNewMap = () => {
  showCreateDialog.value = true
}

// 
const handleCreateMap = () => {
  if (!newMapForm.value.title) {
    ElMessage.warning('')
    return
  }

  const newMap = {
    id: Date.now(),
    title: newMapForm.value.title,
    description: newMapForm.value.description,
    content: {
      data: {
        text: newMapForm.value.title,
        expand: true,
        uid: Date.now().toString(),
        children: []
      }
    },
    createdAt: new Date().toISOString(),
    updatedAt: new Date().toISOString()
  }

  maps.value.unshift(newMap)
  saveMapsToStorage()
  showCreateDialog.value = false
  currentMap.value = newMap
  
  //  mindMap 
  if (!mindMap.value) {
    initMindMap()
  }
  
  //  DOM 
  nextTick(() => {
    loadMap(newMap)
  })
  
  ElMessage.success('')
}

// 
const saveCurrentMap = () => {
  if (!currentMap.value) return
  
  debugger
  const data = mindMap.value?.getData() || {}
  
  // 
  const savedData = {
    data: {
      ...data.data,
      children: data.data?.children || []
    }
  }

  const index = maps.value.findIndex(m => m.id === currentMap.value.id)
  if (index > -1) {
    maps.value[index] = {
      ...currentMap.value,
      content: savedData,
      updatedAt: new Date().toISOString()
    }
    saveMapsToStorage()
    console.log(':', savedData) // 
  }
}

// 
const exportMap = () => {
  // TODO: 
  ElMessage.info('...')
}

// 
const zoomIn = () => {
  if (mindMap.value) {
    const currentZoom = mindMap.value.getZoom()
    mindMap.value.setZoom(currentZoom + 0.1)
  }
}

const zoomOut = () => {
  if (mindMap.value) {
    const currentZoom = mindMap.value.getZoom()
    mindMap.value.setZoom(Math.max(0.1, currentZoom - 0.1))
  }
}

const resetZoom = () => mindMap.value?.setZoom(1)

// /
const undo = () => mindMap.value?.undo()
const redo = () => mindMap.value?.redo()

// 
const handleMapCommand = async (command, map) => {
  let index;
  let duplicatedMap;

  switch (command) {
    case 'rename':
      try {
        const { value: newTitle } = await ElMessageBox.prompt(
          '',
          '',
          {
            confirmButtonText: '',
            cancelButtonText: '',
            inputValue: map.title,
            inputValidator: (value) => {
              if (!value) {
                return ''
              }
              return true
            }
          }
        )
        index = maps.value.findIndex(m => m.id === map.id)
        if (index > -1) {
          maps.value[index] = {
            ...map,
            title: newTitle,
            updatedAt: new Date().toISOString()
          }
          saveMapsToStorage()
          ElMessage.success('')
        }
      } catch (err) {
        // 
      }
      break
    case 'duplicate':
      duplicatedMap = {
        ...map,
        id: Date.now(),
        title: `${map.title} - `,
        createdAt: new Date().toISOString(),
        updatedAt: new Date().toISOString()
      }
      maps.value.unshift(duplicatedMap)
      saveMapsToStorage()
      ElMessage.success('')
      break
    case 'delete':
      try {
        await ElMessageBox.confirm('', '', {
          type: 'warning'
        })
        index = maps.value.findIndex(m => m.id === map.id)
        if (index > -1) {
          maps.value.splice(index, 1)
          saveMapsToStorage()
          if (currentMap.value?.id === map.id) {
            currentMap.value = null
            mindMap.value?.setData({
              data: {
                text: '',
                expand: true,
                uid: Date.now().toString(),
                children: []
              }
            })
            mindMap.value?.setLayout('mindMap')
            mindMap.value?.render()
          }
          ElMessage.success('')
        }
      } catch (err) {
        // 
      }
      break
  }
}

// 
const resetMindMap = () => {
  mindMap.value?.setData({
    data: {
      text: '',
      expand: true,
      uid: Date.now().toString(),
      children: []
    }
  })
  mindMap.value?.setLayout('mindMap')
  mindMap.value?.render()
}

// 
const formatDate = (date) => {
  if (!date) return ''
  return new Date(date).toLocaleString()
}

// 
const saveMapsToStorage = () => {
  localStorage.setItem('mindmaps', JSON.stringify(maps.value))
}

const loadMapsFromStorage = () => {
  const stored = localStorage.getItem('mindmaps')
  if (stored) {
    maps.value = JSON.parse(stored)
    // 
    if (maps.value.length > 0) {
      loadMap(maps.value[0])
    } else {
      resetMindMap()
    }
  }
}

// 
onMounted(() => {
  // 
  loadMapsFromStorage()
  // 
  if (maps.value.length > 0) {
    nextTick(() => {
      initMindMap()
    })
  }
})

onBeforeUnmount(() => {
  if (saveTimeout) {
    clearTimeout(saveTimeout)
  }
  mindMap.value?.destroy()
})

// 
const initMindMap = () => {
  if (!mindMapContainer.value) return
  
  // 
  const initialData = {
    data: {
      text: '',
      expand: true,
      uid: Date.now().toString(),
      children: []
    }
  }

  mindMap.value = new MindMap({
    el: mindMapContainer.value,
    data: initialData,
    width: mindMapContainer.value.clientWidth,
    height: mindMapContainer.value.clientHeight,
    layout: 'mindMap',
    draggable: true,
    keyboard: true,
    contextMenu: true,
    theme: 'classic',
    themeConfig: {
      nodeMargin: 20,
      lineWidth: 2,
      lineColor: '#666'
    },
    viewportOptions: {
      padding: 50
    }
  })

  // 
  mindMap.value.on('data_change', () => {
    saveCurrentMap()
  })

  //  DOM 
  nextTick(() => {
    mindMap.value.render()
  })
}
</script>

<style scoped>
.mindmap-container {
  height: 100%;
  display: grid;
  grid-template-columns: 250px 1fr;
  grid-template-rows: auto 1fr;
  gap: 1px;
  background: var(--el-border-color-light);
}

.toolbar {
  grid-column: 1 / -1;
  padding: 8px 16px;
  background: white;
  display: flex;
  gap: 16px;
  align-items: center;
  border-bottom: 1px solid var(--el-border-color-light);
}

.sidebar {
  background: white;
  display: flex;
  flex-direction: column;
}

.map-list-header {
  padding: 16px;
  border-bottom: 1px solid var(--el-border-color-light);
}

.map-list {
  flex: 1;
  overflow-y: auto;
  padding: 8px;
}

.map-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 12px;
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.3s;
}

.map-item:hover {
  background: var(--el-fill-color-light);
}

.map-item.active {
  background: var(--el-color-primary-light-9);
}

.map-info h4 {
  margin: 0;
  font-size: 14px;
  color: var(--el-text-color-primary);
}

.map-info p {
  margin: 4px 0 0;
  font-size: 12px;
  color: var(--el-text-color-secondary);
}

.main-content {
  background: white;
  position: relative;
}

.empty-state {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: white;
}

.empty-state :deep(.el-empty) {
  padding: 40px;
}

.empty-state :deep(.el-button) {
  margin-top: 16px;
}

.mindmap-wrapper {
  position: absolute;
  inset: 0;
}

.el-button-group {
  display: flex;
  gap: 1px;
}

.el-button-group .el-button {
  border-radius: 0;
}

.el-button-group .el-button:first-child {
  border-radius: 4px 0 0 4px;
}

.el-button-group .el-button:last-child {
  border-radius: 0 4px 4px 0;
}
</style> 